<template>
  <div class = "chatting">
    <div style = "padding:.5rem">{{clazz.name}}</div>
    <div class = "msgs">
      <div class = "none-default" style = "padding:4rem 0;text-align:center" v-show="msgs.length == 0">没有任何消息</div>
      <div class = "msgs-container" ref = "msgContainer">
        <div v-for="item in msgs" :key="item.id">
        <div class = "msg msg-odd" v-if="item.targetuser != targetuserId" >
          <div class = "main">
            <div><Avatar :src="targetuser.icon" shape="square" size = "large" :title = "targetuser.name"/></div>
            <div style = "margin-left:1rem;background:#E4E4E4;padding:.5rem;border-radius:5px;max-width:80%;">
                {{item.msg}}
            </div>
          </div>
          <div class = "bottom">{{$moment(item.createdatetime).format('LLLL')}}</div>
        </div>
        <div class = "msg msg-even" v-if="item.targetuser == targetuserId">
          <div class = "main">
            <div style = "margin-right:1rem;background:#b2e281;padding:.5rem;border-radius:5px;max-width:80%;">
              {{item.msg}}
            </div>
            <div><Avatar :src="icon" size = "large" :title = "name"/></div>
          </div>
          <div class = "bottom">{{$moment(item.createdatetime).format('LLLL')}}</div>
        </div>
        </div>
      </div>
    </div>
    <div class = "sendmsg">
      <div style = "margin-bottom:.5rem"><Icon type="paperclip" style = "font-size:2.5rem;margin:0 1rem;"></Icon><span>(附件)</span></div>
      <div>
        <Input v-model="msg" type="textarea" :rows = "4" placeholder="Enter something..." @on-keypress = "preSendMsg"></Input>
      </div>
      <div style = "text-align:right;margin-top:.5rem">
        <span style = "color:#c8c8c8">Ctrl + Enter 发送</span>
        <Button type = "default" size = "large" style = "width:8rem;margin-left:2rem;" @click = "sendMsg">发送</Button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'chatting',
  data () {
    return {
      msgs:[1,2],
      msg:'',
      targetuser:null,
      currentInterval:'',
      msgContainerScrollIntoBottom:false
    }
  },
  created () {
    
  },
  methods: {
    preSendMsg(e){
      if(e.keyCode == 10){
        this.sendMsg();
      }
    },
    sendMsg(){
      var that = this;
      var params = new URLSearchParams();
      params.append('targetuser',this.targetuserId);
      params.append('msg',this.msg); 
      this.$axios.post('saveMsg.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.msgs.push(res.data.obj);
          that.$refs.msgContainer.scrollIntoView(false);
          setTimeout(()=>{
            that.$refs.msgContainer.scrollIntoView(false);
          },500);
          that.msg = '';
          that.$Message.success(res.data.msg);
        }else{
          alert("出错了，请重试或重新登录。");
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    getMsg(){
      var that = this;
      this.$axios.get('getMsg.aurora',{
        params:{
          targetUserId:that.targetuserId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.msgs = res.data.obj;
          //第一次获取到消息时自动滚动到底部
          if(that.msgContainerScrollIntoBottom == false){
            that.$refs.msgContainer.scrollIntoView(false);
            setTimeout(()=>{
            that.$refs.msgContainer.scrollIntoView(false);
          },1000);
            that.msgContainerScrollIntoBottom = true;
          }
        }else {
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    startChatting(member){
      this.targetuser = member;
      this.getMsg();
      this.currentInterval = setInterval(()=>{
        this.getMsg();
      },5000);
    },
    stopChatting(){
      clearInterval(this.currentInterval);
      this.msgs.splice(0,this.msgs.length);
      this.msgContainerScrollIntoBottom = false;
    }
  },
  computed: {
    targetuserId(){
      if(this.targetuser == null){
        return '';
      }else {
        return this.targetuser.id;
      }
    },
    currentUser(){
      return this.$store.getters.getUser;
    },
    clazz(){return this.$store.getters.getCurrentClass},
    icon(){
      if(this.currentUser == null){
        return '';
      }else{
        return this.currentUser.icon;
      }
    },
    name(){
      if(this.currentUser == null){
        return '';
      }else{
        return this.currentUser.name;
      }
    }
  }
}
</script>
<style lang="less" scoped>
  @bor-c:#c8c8c8;
  .chatting {
    background: white;
    .msgs {
      border-top:1px solid @bor-c;
      border-bottom:1px solid @bor-c;
      max-height:14rem;
      overflow: auto;
      .msgs-container {
        .msg {
            margin-top:.5rem;
          .main {
            display: flex;
            align-items:center;
          }
          .bottom {
            color:#aaa;
          }
        }
        .msg-odd {
          text-align: left;
          .main {
            justify-content: flex-start;
          }
        }
        .msg-even {
          text-align: right;
          .main {
            justify-content: flex-end;
          }
        }


      }

    }
    .sendmsg {
      
    }

  }

</style>



