<template>
  <div id = "sharefile">
    <div class = "addfile" v-if= "userType == 1">
      <a href = "javascript:void(0)" @click = "onEdit = true" v-show = "!onEdit">
        <div class = "addfile-start">
          <Icon type="plus"></Icon>&nbsp;
          <span>添加文件</span>
        </div>
      </a>
      <div class = "addfile-edit" v-show = "onEdit">
        <div>
          上传文件至“&nbsp;<strong>{{currentFolderName}}</strong>&nbsp;”：
        </div>
        <div>
          <Upload
              ref = "upload"
              multiple
              type="drag"
              with-credentials
              :on-success = "uploadFileSuccess"
              :on-remove = "removeTobeSaveFile"
              action="/eclass/tea/uploadSharefile">
              <div style="padding: 20px 0">
                  <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                  <p>点击或拖动上传文件<strong>（文件上传后，如无必要，请不要随便移除列表中的文件！)</strong></p>
                  <p style = "color:#aaa">（上传的文档、图片类型，上传后可在线预览）</p>
                  <p><strong>记得点击确定，否则文件无效</strong></p>
              </div>
          </Upload>
        </div>
        <div class = "addfile-edit-footer">
          <div><Button >导入文件</Button></div>
          <div><Button @click = "onEdit = false">取消</Button><Button type = "primary" @click = "saveUploadFiles()">确定</Button></div>
        </div>
      </div>
    </div>
    <div class = "file-list">
      <div class = "file-list-option">
        <div class = "breadcrumb" style = "overflow:hidden">
          <Breadcrumb>
              <BreadcrumbItem><a href = "javascript:void(0)" @click = "getSharefiles">All</a></BreadcrumbItem>
              <BreadcrumbItem v-for = "(item,index) in navstack" :key = "item.id"><a href = "javascript:void(0)" @click = "navstackEnterfolder(item,index)">{{item.name}}</a></BreadcrumbItem>
          </Breadcrumb>
        </div>
        <div class = "visio-option">
          <span v-if= "userType == 1"><Button type = "text" @click = "newFolderModal = true"><Icon type="plus"></Icon>&nbsp;新建文件夹</Button></span>
          <span><Button type = "text">列表</Button></span>
          <span><Button type = "text">方格</Button></span>
        </div>
      </div>
      <div class = "list">
         <div class = "item foldertype" v-for = "item in folders" :key="item.id">
          <div class = "icon" @click.stop = "enterFolder(item)"><img src = "../../assets/images/icon/folder.png"/></div>
          <div class = "name" @click.stop = "enterFolder(item)"><a href = "javascript:void(0)">{{item.name}}</a></div>
          <div class = "hovershow option" v-if = "userType == 1">
            <span><a href = "javascript:void(0)">保存</a></span>
            <span><a href = "javascript:void(0)">编辑</a></span>
            <span>
              <Dropdown>
                <a href="javascript:void(0)">
                  更多
                </a>
                <DropdownMenu slot="list">
                    <DropdownItem><Button type = "text">权限设置</Button></DropdownItem>
                    <DropdownItem><Button type = "text" @click = "deleteFoder(item)">删除</Button></DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </span>
          </div>
          <div class = "hovershow option" v-if = "userType == 0">
            <span style = "color:red"><a href = "javascript:void(0)">不支持下载</a></span>
          </div>
          <div class = "option hoverhidden">
            <span>0次下载</span>
          </div>
        </div>
        <div class = "item filetype" v-for = "item in sharefiles" :key="item.id" title = "点击可在线预览文件">
          <div class = "icon" @click = "viewfile(item)"><img src = "../../assets/images/icon/file-code.png"/></div>
          <div class = "name" @click = "viewfile(item)">{{item.name}}</div>
          <div class = "hovershow option" v-if="userType==1">
            <span><a :href = "'/eclass/downloadSharefile.file?sharefileId='+ item.id" :download="item.name">下载</a></span>
            <span ><a href = "javascript:void(0)">移入</a></span>
            <span><a href = "javascript:void(0)">保存</a></span>
            <span>
              <Dropdown>
                <a href="javascript:void(0)">
                  更多
                </a>
                <DropdownMenu slot="list">
                    <DropdownItem><Button type = "text">使用统计</Button></DropdownItem>
                    <DropdownItem><Button type = "text">权限设置</Button></DropdownItem>
                    <DropdownItem><Button type = "text" @click = "deleteSharefile(item)">删除</Button></DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </span>
          </div>
          <div class = "hovershow option" v-if="userType==0">
            <span><a :href = "'/eclass/downloadSharefile.file?sharefileId='+ item.id" :download="item.name">下载</a></span>
          </div>
          <div class = "option hoverhidden">
            <span>{{item.downloadtimes}}&nbsp;次下载</span>
          </div>
        </div>
      </div>
    </div>

    <div class = "modals">
      <Modal
        v-model="newFolderModal"
        :scrollable = "true"
        title="新建文件夹"
        :mask-closable = "false"
        @on-ok="newFolderOK"
        >
        <div>
          <div style = "margin:2rem 0; font-size:1.2rem">
            您将在"&nbsp;<strong>{{currentFolderName}}</strong>&nbsp;"下新建一个文件夹
          </div>
          <Form label-position="top">
            <FormItem label="文件夹名称:" :required = "true">
                <Input v-model="folderName" placeholder = "请输入文件夹名称"></Input>
            </FormItem>
          </Form>
        </div>
      </Modal>

    </div>
  </div>
</template>
<script>
export default({
  data(){
    return{
      onEdit:false,
      newFolderModal:false,
      folderName:'',
      navstack:[],
      currentFolder:null,
      tobeSaveFiles:[],
    }
  },
  created(){
    this.$store.commit('setCurrentNav','sharefile');
    this.getSharefiles();
  },
  methods:{
    getSharefiles(){
      this.navstack.splice(0,this.navstack.length)
      var that =this;
      this.$axios.get('getSharefiles.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.currentFolder = res.data.obj; 
          
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    newFolderOK(){
      var that =this;
      this.$axios.get('tea/createFolder.aurora',{
        params:{
          pfid:that.currentFolder.id,
          name:that.folderName
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.currentFolder = res.data.obj;
          that.folderName = '';
          if(that.navstack.length>0){
            that.navstack.pop();
            that.navstack.push(that.currentFolder);
          }
        }
      })
      .catch(function(e){
        console.log(e);
      });

    },
    enterFolder(item){
      this.currentFolder = item;
      this.navstack.push(item);
    },
    navstackEnterfolder(item,index){
      this.currentFolder = item;
      this.navstackBackTo(index);
    },
    navstackBackTo(index){
      this.navstack.splice(index+1,this.navstack.length-1);
    },
    deleteFoder(item){
      this.$Modal.confirm({
                    title: '确认删除" '+item.name+' "？',
                    content: '<p>该文件夹内的所有相关文件都将被删除</p>',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.delFolderOK(item.id);
                    }
                });
    },
    delFolderOK(id){
      var that =this;
      this.$axios.get('tea/deleteFolder.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.arrayRemoveById(that.folders,id);
          that.$Modal.remove();
        }
      })
      .catch(function(e){
        console.log(e);
      });


    },
    deleteSharefile(item){
      this.$Modal.confirm({
                    title: '确认删除" '+item.name+' "？',
                    content: '',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.deleteSharefileOK(item.id);
                    }
                });

    },
    deleteSharefileOK(id){
       var that =this;
      this.$axios.get('tea/deleteSharefile.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.arrayRemoveById(that.sharefiles,id);
          that.$Modal.remove();
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    arrayRemoveById(arr,id){
      for(var i = 0; i < arr.length; i++){
            if(arr[i].id == id)
              arr.splice(i,1);
      }
    },
    //文件上传部分
    uploadFileSuccess(res, file, fileList){
        this.tobeSaveFiles.push(res.obj);
    },
    removeTobeSaveFile(file, fileList){
      for(var i = 0; i < this.tobeSaveFiles.length; i++){
            if(this.tobeSaveFiles[i] == file.response.obj){
              this.tobeSaveFiles.splice(i,1);
            }
      }
      var that = this;
        this.$axios.get('tea/removeSharefile.aurora',{
          params:{
            fid:file.response.obj,
          }
        })
        .then(function(res){
          if(res.data && res.data.success){
            that.$Message.success(res.data.msg);
          }
        })
        .catch(function(e){
          console.log(e);
        });

    },
    saveUploadFiles(){
      var that = this;
      var params =  new URLSearchParams(); 
      params.append('fid',this.currentFolder.id);
      params.append('files',this.tobeSaveFiles);
      this.$axios.post('tea/saveUploadSharefile.aurora',params)
      .then(function(res){
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.currentFolder = res.data.obj;
          that.folderName = '';
          that.onEdit = false;
          that.$refs.upload.clearFiles();//清空已上传文件列表
          if(that.navstack.length>0){
            that.navstack.pop();
            that.navstack.push(that.currentFolder);
          }
        }
      })
      .catch(function(e){
        console.log(e);
      });
        
    },
    cancelSharefile(){
      this.tobeSaveFiles.splice(0,this.tobeSaveFiles.length);
      this.onEdit = false;
    },
    //文件下载和预览部分
    viewfile(item){
      this.$store.commit('setCurrentObject',item.userfile);
      this.$router.push('/sharefileviewer');
    }
  },
  computed:{
    userType(){
      return this.$store.getters.getType;
    },
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    sharefiles(){
      if(this.currentFolder){
        return this.currentFolder.sharefiles;
      }else{
        return [];
      }
    },
    folders(){
      if(this.currentFolder){
        return this.currentFolder.childfolders;
      }else{
        return [];
      }
    },
    currentFolderName(){
      if(this.currentFolder && this.currentFolder.name){
        return this.currentFolder.name;
      }else{
        return '';
      }
      
    }
  },
  watch:{
    onEdit(){
      if(this.onEdit == false){
        this.$emit('headershow');
        this.$refs.upload.clearFiles();//清空已上传文件列表
      }else{
        this.$emit('headerhidden');
      }
    },
    classId(){
      this.getSharefiles();
    }
  }
})
</script>


<style lang = "less" scoped>
  @bor-c:#c8c8c8;
  #sharefile {
    padding-top:1.5rem;
  }
  .addfile {
    background-color:white;
    .addfile-start {
      padding:1rem 2rem;
      border:1px solid @bor-c;
    }
    .addfile-edit {
      border:1px solid @bor-c;
      padding:1rem;
      > div+div {
        margin-top:1rem;
      }
      .addfile-edit-footer {
        display:flex;
        justify-content:space-between;
        background-color:#ddd;
        padding: 2rem;
        > div+div > * {
          margin-left: 1rem;
        }
      }
    }
  }
  .file-list {
    min-height:40rem;
    background-color:white;
    margin-top:1.5rem;
    border:1px solid @bor-c;
    .file-list-option {
      padding:.5rem 1rem;
      display: flex;
      justify-content:space-between;
      align-items:center;
      border-bottom:1px solid @bor-c;
    }
    .list {
      padding:2rem 3rem;
      display:flex;
      flex-wrap:wrap;
      .item {
        padding:.5rem;
        text-align: center;
        position:relative;
        margin:1rem;
        width:10rem;
        height:12rem;
        display: flex;
        flex-direction:column;
        justify-content: flex-end;
        > div+div {
          margin-top:.5rem;
        }
        .icon{
          img {
            width:70%;
          }

        }
        .name{
          font-size: .6rem;
          height:1.2rem;
          overflow: hidden;

        }
        .option {
          font-size: .6rem;
          display: flex;
          justify-content:space-around;
        }
        .hovershow {
            display: none;
          }

        &:hover {
          background-color: #EFFBFE;
          box-shadow: 2px 2px 5px #888888;
          .hovershow {
            display: block;
          }
          .hoverhidden {
            display: none;
          }
          

        }
      }

    }
  }
</style>


