<template>
  <div class = "pdfviewer">
    <Spin size="large" fix v-if="spinShow">文件转码中</Spin>
    <div>
      <iframe ref = "pdfview">oops,您的浏览器不支持iframe !</iframe>
    </div>
  </div>
</template>
<script>
export default({
  name:'pdfviewer',
  data(){
    return {
      fileId:'',
      spinShow:false,
      iframesrc:'static/pdfjs/web/viewer.html',
    }
  },
  mounted(){
    this.$refs.pdfview.src = this.iframesrc;
  },
  methods:{
    start(id){
      this.checkFile(id);
    },
    checkFile(id){
      console.log('checkfile');
      var that = this;
      this.$axios.get('checkFile.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.loadfile(id);
        }else{
          that.convertFile(id);
          that.$Notice.open({
              title: '文件转码中',
              desc: '文件正在转码中，请稍后......',
              duration: 3
            });
        }
      })
      .catch(function(){

      });

    },
    convertFile(id){
      console.log('convertfile');
      this.spinShow = true;
      var that = this;
      this.$axios.get('convertFile',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.spinShow = false;
          that.loadfile(id);
        }else{
          this.$Modal.confirm({
              title: '出错了',
              content: '系统出现了问题，请返回上级菜单，然后重试',
              scrollable:true,
              onOk: () => {    
              }
            });
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    loadfile(id){
      var that = this;
      this.$refs.pdfview.src = this.iframesrc;
      if (this.$refs.pdfview.attachEvent){  
          that.$refs.pdfview.attachEvent("onload", function(){ // IE  
            that.$refs.pdfview.contentWindow.loadpdf('getFile?id='+ id);
          });  
      } else {  
          this.$refs.pdfview.onload = function(){ // 非IE  
            that.$refs.pdfview.contentWindow.loadpdf('/eclass/getFile?id=' + id);
          };  
      }  
      console.log('loadfile');
    }
  },
  watch: {
    fileId(){
      this.start(this.fileId);
    }
  }
})
</script>
<style lang = "less" scoped>
  .pdfviewer {
    width:100%;
    height:100vh;
    > div {
      width: inherit;
      height: inherit;
    }
    iframe {
      width: inherit;
      height: inherit;
    }
  }
</style>


