<template>
  <div id = "classroom">
    <transition name="slide-fade">
    <header @click.prevent = "isDrawerOut = false" v-show="headerShow">
      <!--头部菜单-->
      <div class = "menubar">
        <div>
        </div>
        <div class = "user-option">
          <span><router-link to="/login"><Badge :value="200" :max="99" class="item"><Icon type="ios-bell-outline" size = "28"></Icon></Badge></router-link></span>
          <span>
            <Dropdown>
                <span>
                  <Avatar :src = "icon == '' ? 'https://i.loli.net/2017/08/21/599a521472424.jpg': icon" size = "large" :title = "name"/>
                </span>
                <DropdownMenu slot="list">
                  <DropdownItem><router-link to="/classes/setting"><Button type="text" size="small"><Icon type="android-settings"></Icon>个人设置</Button></router-link></DropdownItem>
                  <DropdownItem><Button type="text" size="small"><Icon type="android-download"></Icon>文库下载</Button></DropdownItem>
                  <DropdownItem><Button type="text" size="small" @click="logout"><Icon type="log-out"></Icon>退出登录</Button></DropdownItem>
                </DropdownMenu>
            </Dropdown>
          </span>
        </div>
      </div>
      <div class = "center">
        <h1>{{clazz.name}}</h1>
        <p>请使用以下邀请码邀请同学加入班级</p>
        <div>
        <Dropdown>
            <Button type = "text" style = "color:white;font-size:1.2rem;border:1px solid white">{{clazz.invitecode}}&nbsp;&nbsp;<Icon type="arrow-down-b" v-if = "userType == 1"></Icon></Button>
            <DropdownMenu slot="list" v-if = "userType == 1">
                <DropdownItem><Button type = "text" size="small" disabled>停用</Button></DropdownItem>
                <DropdownItem><Button type = "text" size="small" @click = "resetInviteCode">重置</Button></DropdownItem>
            </DropdownMenu>
        </Dropdown>
        </div>
      </div>
      <div class = "bottom">
        <div><a href = "javascript:void(0)"  @click = "changeBagModal = true" v-if = "userType == 1">选择主题图片</a></div>
        <div>
          <router-link to = "/course/checkin" v-if = "userType == 1"><Icon type="ios-people"></Icon>&nbsp;<span>考勤</span></router-link>
          <router-link to = "/course/performance" v-if = "userType == 1"><Icon type="stats-bars"></Icon>&nbsp;<span>表现</span></router-link>
          <router-link to = "/course/member"><Icon type="person-stalker"></Icon>&nbsp;<span>成员</span></router-link>
          <router-link to = "/course/score"><Icon type="ios-star"></Icon>&nbsp;<span>成绩</span></router-link>
        </div>
      </div>
    </header>
    </transition>
    <nav>
      <a href = "javascript:void(0)"><span @click = "to('homework')" :class = "{'nav-selected':navSelected == 'homework'}">作业</span></a>
      <a href = "javascript:void(0)"><span @click = "to('topic')" :class = "{'nav-selected':navSelected == 'topic'}">话题</span></a>
      <a href = "javascript:void(0)"><span @click = "to('sharefile')" :class = "{'nav-selected':navSelected == 'sharefile'}">资料</span></a>
      <a href = "javascript:void(0)"><span @click = "to('bulletin')" :class = "{'nav-selected':navSelected == 'bulletin'}">公告</span></a>
    </nav>
    <div class = "main">
      <v-drawer></v-drawer>
      <div class = "main-container">
        <transition name="slide-fade">
        <router-view @headerhidden = "headerShow = false" @headershow = "headerShow = true" ></router-view>
        </transition>
      </div>
    </div>
    <div class = "modals">
      <Modal
        v-model="changeBagModal"
        :scrollable = "true"
        title="选择主题图片"
        :mask-closable = "false"
        :width = "80"
        >
        <div class = "bacg-picker">
          <img src = "../assets/images/headerbackg/01.jpg"/>
          <img src = "../assets/images/headerbackg/02.jpg"/>
          <img src = "../assets/images/headerbackg/03.jpg"/>
          <img src = "../assets/images/headerbackg/04.jpg"/>
        </div>
      </Modal>

      <Modal
        v-model="checkinModal"
        :scrollable = "true"
        title="班级考勤"
        :mask-closable = "false"
        @on-ok = "checkingin"
        @on-cancel = "checkinCode=''"
        >
        <div class = "checkin-dlg">
           <Form>
              <FormItem label = "输入4位考勤码(数字)">
                  <input type="text" v-model = "checkinCode" :maxlength = "4" autofocus>
                  </input>
              </FormItem>
            </Form>
        </div>
      </Modal>
    </div>
    <v-footer></v-footer>
  </div>
</template>
<script>
import drawer from '@/components/common/drawer'
import footer from '@/components/common/footer'
import homework from '@/components/classroom/homework'
import submithomework from '@/components/classroom/submithomework'
import topic from '@/components/classroom/topic'
import bulletin from '@/components/classroom/bulletin'
import sharefile from '@/components/classroom/sharefile'
import 'static/editor/ueditor.config.js'
import 'static/editor/ueditor.all.min.js'
import 'static/editor/lang/zh-cn/zh-cn.js'
export default({
  name:'classroom',
  data(){
    return {
      checkinModal:false,
      checkinCode:'',
      checkinId:'',
      headerShow:true,
      changeBagModal:false
    }
  },
  components:{
    'v-footer':footer,
    'v-drawer':drawer
  },
  created () {
    if(this.$store.getters.getCurrentClass == null){
      this.$router.push('/classes');
    }
  },
  mounted () {
    if(this.userType == 0){
      this.getCheckin();
    }
  },
  methods:{
    to(targ){
      this.headerShow = true;
      this.$router.push('/classroom/' + targ);
    },
    logout(){
      var that = this;
      this.$axios.get('logout')
      .then(function(res){
        if(res.data.success == true){
          that.$router.push('/login');
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    resetInviteCode(){
      var that = this;
      this.$axios.get('tea/resetInviteCode.aurora',{
        params:{
          id:that.clazz.id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.$store.commit('setCurrentClass',res.data.obj);
          that.$Message.success(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e);

      })
    },
    getCheckin(){
      var that = this;
      this.$axios.get('stu/getUncheckedCheckin.aurora',{
        params:{
          classId:that.clazz.id
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
         that.checkinModal = true;
         that.checkinId = res.data.obj;
        }
      })
      .catch((e)=>{
        console.log(e);
      })

    },
    checkingin(){
      var that = this;
      this.$axios.get('stu/checkingin.aurora',{
        params:{
          id:that.checkinId,
          checkinCode:that.checkinCode
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
         this.$Modal.confirm({
                    title: '已签到',
                    content: '<p>签到成功</p>',
                    scrollable:true,
                    onOk: () => {
                      that.checkinModal = false;
                      that.checkinCode = '';
                    },
                    onCancel:()=>{
                      that.checkinModal = false;
                      that.checkinCode = '';
                    }
                });
         
        }else{
          this.$Modal.confirm({
                    title: '考勤码错误',
                    content: '<p>请认真填写考勤码</p>',
                    scrollable:true,
                    onOk: () => {
                      that.checkinModal = true;
                      that.checkinCode = '';
                    },
                    onCancel:()=>{
                      that.checkinModal = true;
                      that.checkinCode = '';
                    }
                });
        }
      })
      .catch((e)=>{
        console.log(e);
      })

    }
  },
  computed:{
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    icon(){
      if(this.user == null){
        return '';
      }else{
        return this.user.icon;
      }
    },
    clazz(){return this.$store.getters.getCurrentClass},
    navSelected(){
      return this.$store.getters.getCurrentNav;
    },
    userType(){
      return this.$store.getters.getType;
    }
  },
  homework:homework,
  submithomework:submithomework,
  topic:topic,
  sharefile:sharefile,
  bulletin:bulletin
})
</script>
<style lang = "less" scoped>
@bor-c:#c8c8c8;
header {
  position:relative;
  height: 20rem;
  padding:1rem 2rem;
  background-color:rgba(255, 241, 250, 0.7);
  background-image:url(../assets/images/headerbackg/03.jpg);
	background-size:cover;
  .menubar {
    display: flex;
    justify-content:space-between;
    align-items: center;

    .user-option {
      padding:0 2rem;
      display: flex;
      align-items: center;
      > span {
        margin-left:3rem;
      }

    }
  }
  .center {
    color:white;
    text-align: center;
    height: 14rem;
    display: flex;
    flex-direction: column;
    justify-content: center ;
    align-items:center;
    > p, div {
      margin-top:1rem;
    }
  }
  .bottom {
    color:white;
    padding:1rem 2rem;
    padding-right:3rem;
    display: flex;
    justify-content:space-between;
    position: absolute;
    left:0;
    bottom:0;
    right:0;
    > div + div > * {
      margin-left:2rem;
      font-size:1.15rem;

    }

  }
}
nav {
  height: 4rem;
  z-index:1;
	color:#eeedee;
	font-size:1.1rem;
	background-color:#834a79;
	min-height:4rem;
  text-align:center;
  
  span:nth-child(n) {
    display:inline-block;
    height:inherit;
    width:12rem;
    margin:0 .5rem;
    line-height:3.8rem;
    box-sizing:border-box;
    -moz-box-sizing:border-box; /* Firefox */
    -webkit-box-sizing:border-box; /* Safari */
    &:hover {
      border-bottom:1px solid white;
    }
  }
}
.nav-selected {
	border-bottom:1px solid white;
}
.main {
  height:100vh;
  background-color:#e7e4f0;
  overflow-x:hidden;
  width:100vw;
  padding-right: 20px;
 
}
.main-container {
  min-height:30rem;
  width:70%;
  margin:0 auto;
  
}
/*header背景选择对话*/
.bacg-picker {
  width:100%;
  z-index: inherit;
  img {
    opacity: .6;
    width:auto;
    height:auto;
    max-width:40%;
    max-height:20%;
    margin:.5rem;
    &:hover {
      opacity:1;
    }
    
  }

}
/*考勤对话框*/
.checkin-dlg {
  padding:2rem 1rem;
  input {
    outline:none;
	  width:100%;
	  font-size:2.5rem;
	  text-align:center;
	  letter-spacing:1rem;
  }
  
}
/*过渡效果*/
.slide-fade-enter-active {
  transition: all 2s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  /*transform: translateX(10px);*/
  opacity: 0;
}

</style>


