<template>
  <div id = "bulletin">
    <div class = "addbulletin" v-if= "userType == 1">
      <a href = "javascript:void(0)" @click = "onEdit = true" v-show = "!onEdit">
        <div class = "addbulletin-start">
          <Icon type="plus"></Icon>&nbsp;
          <span>添加公告</span>
        </div>
      </a>
      <div class = "addbulletin-edit" v-show = "onEdit">
        <div>
          <Input v-model="bulletinModel.title" placeholder="输入公告标题" size = "large"></Input>
        </div>
        <div>
          <div id="bulletinEditor"></div>
        </div>
        <div class = "addbulletin-edit-footer">
          <div><Button >导入公告</Button></div>
          <div><Button @click = "onEdit = false">取消</Button><Button type = "primary" @click = "editOK">确定</Button></div>
        </div>
      </div>
    </div>
    <div class = "bulletin-list">
      <div class = "bulletin-none-default" v-if = "noBulletins && !onEdit">
        <div>
          <p style = "font-size:1.2rem;font-weight:bold" v-if= "userType == 0">您的班级暂无任何公告。</p>
          <p style = "font-size:1.2rem;font-weight:bold" v-if= "userType == 1">点击上方按钮，您可以添加新的班级公告。</p>
          <p v-if= "userType == 1">您可以在此发布一些重要公告</p>
        </div>
      </div>

      <div class = "list">
        <div v-for =" item in bulletins" :key="item.id">
          <div class = "item-main" v-show="!item.onEdit">
            <div class = "item-main-left">
              <div><h2>{{item.title}}</h2></div>
              <div style = "color:#aaa;"><span>发布人:&nbsp;{{item.author}}</span>&nbsp;&nbsp;<span>发布时间:{{$moment(new Date(item.createdatetime)).format('LLLL')}}</span></div>
              <div>
                <div v-html="item.content"></div>
              </div>
              <div></div>
            </div>
            <div class = "item-main-right">
              <div v-if = "userType == 1">
                <Dropdown trigger="click">
                  <a href="javascript:void(0)">
                    <span><Icon type="ios-keypad"></Icon></span>
                  </a>
                  <DropdownMenu slot="list" style = "text-align:center">
                      <DropdownItem><Button type = "text">设为置顶</Button></DropdownItem>
                      <DropdownItem><Button type = "text" @click = "redit(item)">编辑</Button></DropdownItem>
                      <DropdownItem><Button type = "text" @click = "deleteBulletin(item)">删除</Button></DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </div>
            </div>
          </div>
          <div class = "addbulletin-edit" v-show="item.onEdit">
            <div>
              <Input  v-model= "item.title" placeholder="输入公告标题" size = "large"></Input>
            </div>
            <div>
              <div :id="'bulletinEditor-' + item.id"></div>
            </div>
            <div class = "addbulletin-edit-footer">
              <div></div>
              <div><Button @click = "reditCancel(item)">取消</Button><Button type = "primary" @click = "reditOK(item)">确定</Button></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default({
  name:'bulletin',
  data(){
    return{
      onEdit:false,
      editor:{},
      toolbars: [
        [
          'undo', 'redo', '|',
          'bold', 'italic', 'underline', 'strikethrough', 'superscript', 'subscript','blockquote','touppercase', 'tolowercase','link',"|",
          'forecolor', 'backcolor',"|",
          'fontfamily', 'fontsize', '|',
          'justifyleft', 'justifycenter', 'justifyright',"|",
          'insertcode',
          'spechars','simpleupload','imageleft', 'imageright', 'imagecenter',"|",
          'attachment','horizontal','preview',
          'fullscreen' 
        ]
      ],
      bulletinModel:{
        title:'',
        content:''
      },
      bulletins:[]
      
    }
  },
  created(){
    this.$store.commit('setCurrentNav','bulletin');
    this.getBulletins();
  },
  methods:{
    editOK(){
      var that = this;
      this.bulletinModel.content = this.editor.getContent();
      var params =  new URLSearchParams(); 
      params.append('title',this.bulletinModel.title);
      params.append('content',this.bulletinModel.content);
      params.append('classId',this.classId);
      this.$axios.post('tea/createBulletin.aurora', params)
      .then(function(res){
        if(res.data && res.data.success){
          var temp = res.data.obj;
          that.onEdit = false;
          that.$Message.success(res.data.msg);
          temp.onEdit = false;
          that.bulletins.unshift(temp);
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    getBulletins(){
      var that =this;
      this.$axios.get('getBulletins.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          var temp = res.data.obj;
          for(var key in temp){
            temp[key].onEdit = false;
          }
          that.bulletins = temp;
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    redit(item){
      this.$emit('headerhidden');
      item.onEdit = true;
      UE.delEditor('bulletinEditor-' + item.id);
      item.editor = UE.getEditor('bulletinEditor-' + item.id,{toolbars:this.toolbars});
      item.editor.ready(function(){
        this.setContent(item.content);
      });
    },
    reditCancel(item){
      item.onEdit = false;
      this.$emit('headershow');
      UE.delEditor('bulletinEditor-' + item.id);
    },
    reditOK(item){
      var that = this;
      var content = item.editor.getContent();
      var params =  new URLSearchParams();
      params.append('id',item.id); 
      params.append('title',item.title);
      params.append('content',content);
      this.$axios.post('tea/updateBulletin.aurora', params)
      .then(function(res){
        if(res.data && res.data.success){
          item.onEdit = false;
          that.$Message.success(res.data.msg);
          that.$emit('headershow');
          UE.delEditor('bulletinEditor-' + item.id);
          item.content = content;
        }
      })
      .catch(function(e){
        console.log(e);
      })

    },
    deleteBulletin(item){
      this.$Modal.confirm({
                    title: '确认删除" '+item.name+' "？',
                    content: '',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.delBulletinOK(item.id);
                    }
                });
    },
    delBulletinOK(id){
      var that =this;
      this.$axios.get('tea/deleteBulletin.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.arrayRemoveById(that.bulletins,id);
          that.$Modal.remove();
        }
      })
      .catch(function(e){
        console.log(e);
      });


    },
    arrayRemoveById(arr,id){
      for(var i = 0; i < arr.length; i++){
            if(arr[i].id == id)
              arr.splice(i,1);
      }
    }
  },
  computed:{
    userType(){
      return this.$store.getters.getType;
    },
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    noBulletins(){
      return this.bulletins.length == 0
    }
  },
  watch:{
    onEdit(){
      if(this.onEdit == false){
        this.$emit('headershow');
        this.editor.execCommand('cleardoc');
        UE.delEditor('bulletinEditor');
      }else{
        this.$emit('headerhidden');
        UE.delEditor('bulletinEditor');
        this.editor = UE.getEditor('bulletinEditor',{toolbars:this.toolbars});
      }
    },
    classId(){
      //重要，当班级id变化时重新更新数据；
      this.getBulletins();
    }
  }
})
</script>


<style lang = "less" scoped>
  @bor-c:#c8c8c8;
  #bulletin {
    padding-top:1.5rem;
  }
  .addbulletin {
    background-color:white;
    .addbulletin-start {
      padding:1rem 2rem;
      border:1px solid @bor-c;
    }
  }
  .addbulletin-edit {
      background-color:white;
      border:1px solid @bor-c;
      padding:1rem;
      > div+div {
        margin-top:1rem;
      }
      .addbulletin-edit-footer {
        display:flex;
        justify-content:space-between;
        background-color:#ddd;
        padding: 2rem;
        > div+div > * {
          margin-left: 1rem;
        }
      }
    }
  .bulletin-list {
    margin-top:1.5rem;
    .bulletin-none-default {
      text-align: center;
      height: 8rem;
      background-color:rgba(0,0,0,.03);
      display: flex;
      justify-content:center;
      align-items:center;
    }
    .list {
      margin-bottom:2rem;
      > div + div {
        margin-top:1.5rem;
      }
      .item-main {
        background-color:white;
        display: flex;
        padding:2rem;
        padding-bottom:.5rem;
        border:1px solid @bor-c;
        box-shadow:1px 1px 2px 1px #c8c8c8;
        .item-main-left {
          flex:9;
          max-width:100%;
          overflow: hidden;
          > div {
            + div {
              margin-top:1.2rem;
            }
          }
        }
        .item-main-right {
          flex:1;
          text-align: right;
          
        }
      }
      .item-editor {
        background-color:white;
      }
    }
  }
</style>


