<template>
  <div class = "main">
    <div class = "main-header">
      <div>
        <Dropdown style="margin-left: 20px" v-if = "userType == 1">
          <span class="el-dropdown-link">
            <Icon type="plus-round"></Icon>加入/创建班级<Icon type="arrow-down-b"></Icon>
          </span>
          <DropdownMenu slot="list">
              <DropdownItem><Button type="text" size = "small" @click="modalVisible1 = true">创建班级</Button></DropdownItem>
              <DropdownItem><Button type="text" size = "small" @click="modalVisible2 = true">加入班级</Button></DropdownItem>
          </DropdownMenu>
        </Dropdown>
        
        <Button type="text" size = "small" @click="modalVisible2 = true" v-if = "userType == 0"><Icon type="plus-round"></Icon>加入班级</Button>
        
      </div>
      <div>
        <span><Icon type="navicon-round"></Icon>&nbsp;班级排序</span>
        <span><Icon type="ios-paper-outline"></Icon>&nbsp;归档管理</span>
      </div>
    </div>
    <div class = "main-container">
      <Spin fix size="large" v-if = "loadingClass">Loading...</Spin>
     
      <div class = "item" v-for = "item in classes" :key = "item.id">
        <div class = "item-header">
          <div class = "top">
            <div>
              <a href = "javascript:void(0)" @click = "hrefTo(item)">{{item.name}}</a>
            </div>
            <div>
              <Dropdown trigger="click">
                <span class="el-dropdown-link">
                  <img src = "../../assets/images/icon/menu-item-32.png" style = "width:24px;height:24px;"/>
                </span>
                <DropdownMenu slot="list" v-if = "userType == 1">
                    <DropdownItem><Button type="text" size = "small" @click="editClass(item)">编辑</Button></DropdownItem>
                    <DropdownItem><Button type="text" size = "small" @click="deleteClass(item)">删除</Button></DropdownItem>
                    <DropdownItem><Button type="text" size = "small" @click="modalVisible2 = true">归档</Button></DropdownItem>
                    <DropdownItem><Button type="text" size = "small" @click="modalVisible2 = true">复制班级</Button></DropdownItem>
                    <DropdownItem><Button type="text" size = "small" @click="modalVisible2 = true">打包下载</Button></DropdownItem>
                </DropdownMenu>
                <DropdownMenu slot="list" v-if = "userType == 0">
                    <DropdownItem><Button type="text" size = "small" >归档</Button></DropdownItem>
                    <DropdownItem><Button type="text" size = "small" @click="outClass(item)">退课</Button></DropdownItem>
                    
                </DropdownMenu>
              </Dropdown>
            </div>
          </div>
          <div class = "bottom">角色：{{userRole}}</div>
        </div>
        <div class = "item-main">
          <span>近期作业</span>
          <div class = "homeworks">
            <span><router-link to=""></router-link></span>
            <span><router-link to=""></router-link></span>
          </div>
          
          <div class = "class-info">
            <span><Icon type="person-add"></Icon>&nbsp;邀请码：{{item.invitecode}}</span>
            <div><Icon type="person-stalker"></Icon>&nbsp;成员:&nbsp;{{item.membersum}}</div>
          </div>
        </div>
      </div>
      
    </div>
    
    <div class = "modal">
      <Modal
        v-model="modalVisible1"
        title="创建班级"
        :loading="modalOKloading"
        @on-ok="createClassOK">
        <Form label-position="top" :model="form1">
          <FormItem label="请输入班级名称">
              <Input v-model="form1.name"></Input>
          </FormItem>
        </Form>
      </Modal>
      
      <Modal
        v-model="modalVisible2"
        title="加入班级"
        :loading="modalOKloading"
        @on-ok="joinClassOK ()">
        <Form label-position="top" :model = "form2">
          <FormItem label="请输入班级邀请码">
              <Input v-model="form2.invitecode"></Input>
          </FormItem>
        </Form>
      </Modal>
      
      <Modal v-model="modalVisible3" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span>确认删除班级</span>
        </p>
        <div style="text-align:center">
            <p>该班级删除后不可恢复，该班级内的所有资源都将永远丢失</p>
            <p>确定要删除班级"{{currentOptionClass.name}}"吗?</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large" long @click="deleteClassOK()">是的，删除</Button>
        </div>
      </Modal>
      <Modal
        v-model="modalVisible4"
        title="编辑班级"
        :loading="modalOKloading"
        @on-ok="editClassOK">
        <Form label-position="top" :model="form4">
          <FormItem label="请输入班级名称">
              <Input v-model="form4.name"></Input>
          </FormItem>
        </Form>
      </Modal>
      <Modal v-model="modalVisible5" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span>退出班级</span>
        </p>
        <div style="text-align:center">
            <p>确定要退出班级"{{currentOptionClass.name}}"吗?</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large" long @click="outClassOK()">是的，退出</Button>
        </div>
      </Modal>
   
    </div>
  </div>
</template>

<script>

export default {
  name: 'classes-mian',
  data () {
    return {
      loadingClass:true,
      input:'',
      modalVisible1: false,//create
      modalVisible2: false,//join
      modalVisible3: false,//delete
      modalVisible4: false,//create
      modalVisible5: false,//out
      modalOKloading: true,
      currentOptionClass:{},
      form1:{
        name:''
      },
      form2:{
        invitecode:''
      },
      form4:{
        name:''
      }
    }
  },
  created(){
    this.getClass();
    this.notice();
    this.getUserInfo();
  },
  methods:{
    hrefTo(item){
      this.$store.commit('setCurrentClass',item);
      this.$router.push('/classroom/homework');
    },
    getUserInfo(){
      var that = this;
      this.$axios.get('getUserInfo.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.$store.commit('setUser',res.data.obj);
        }else{
        }
      })
      .catch(function(e){
      });
    },
    createClassOK () {
      var that = this;
      var searchparams = new URLSearchParams();//Using application/x-www-form-urlencoded format  
      searchparams.append('name',this.form1.name);
      this.$axios.post('tea/createClass.aurora', searchparams)
      .then(function(res){
        if(res.data.success == true){
          that.form1.name = '';
          that.$Message.success(res.data.msg);
          that.$store.commit('addClass',res.data.obj);
        }
      })
      .catch(function(e){
        console.log(e);
      });
      this.modalVisible1 = false;
    },
    joinClassOK () {
      var that = this;
      this.$axios.get('joinClass.aurora',{
        params:{
          invitecode:that.form2.invitecode
        }
      })
      .then(function(res){
        if(res.data.success == true){
          that.$store.commit('addClass',res.data.obj);
          
        }else{
          that.$Message.warning(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e);
      }); 
      this.modalVisible2 = false;
      this.form2.invitecode = '';
    },
    deleteClassOK(){
      var that = this;
      this.$axios.get('tea/deleteClass.aurora',{
        params:{
          id:that.currentOptionClass.id
        }
      })
      .then(function(res){
        if(res.data.success == true)
          that.$store.commit('removeClassById',that.currentOptionClass.id);
          that.$Message.success(res.data.msg);
      })
      .catch(function(e){
        console.log(e);
      });
      this.modalVisible3 = false;
    },
    editClassOK(){
      var that = this;
      this.$axios.get('tea/editClass.aurora',{
        params:{
          id:that.currentOptionClass.id,
          name:that.form4.name
        }
      })
      .then(function(res){
        if(res.data.success == true)
          that.currentOptionClass.name = that.form4.name;
          that.$Message.success(res.data.msg);
          that.form4.name = '';
      })
      .catch(function(e){
        console.log(e);
      });
      this.modalVisible4 = false;
    },
    outClassOK(){
      var that = this;
      this.$axios.get('stu/outClass.aurora',{
        params:{
          id:that.currentOptionClass.id,
        }
      })
      .then(function(res){
        if(res.data.success == true){
          that.$store.commit('removeClassById',that.currentOptionClass.id);
          that.$Message.success(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e);
      });
      this.modalVisible5 = false;
    },
    getClass(){
      var that = this;
      this.$axios.get('getClass.aurora')
      .then(function(res){
        that.loadingClass = false;
        if(res.data.success == true){
          that.$store.commit('setClasses',res.data.obj);
        }else{
          
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    deleteClass(clazz){
      this.currentOptionClass = clazz;
      this.modalVisible3 = true;
    },
    editClass(clazz){
      this.currentOptionClass = clazz;
      this.modalVisible4 = true;
      this.form4.name = this.currentOptionClass.name;
    },
    outClass(clazz){
      this.currentOptionClass = clazz;
      this.modalVisible5 = true;
    },
    enterClass(item){
      this.$store.commit('setCurrentClass',item);
      this.$router.push('/classroom');
    },
    notice(){
      this.$Notice.open({
                    title: '关于本系统',
                    desc: '本系统由周兴明进行开发和维护，部分功能不能使用还请谅解，本系统将进行持续改进，\
                    致力于提供更加优质的服务。如有问题或建议，请联系周兴明（2982968104@qq.com）.',
                    duration: 5,

                });
    }
  },
  computed:{
    userRole(){
      if(this.userType == 1)
        return '教师';
      else 
        return '学生';
    },
    userType(){
        return this.$store.state.type;
    },
    classes(){
      return this.$store.state.classes;
    }
  
  }
}
</script>

<style scoped lang = "less">

@bor-c:#c8c8c8;

.main {
  padding:2rem 3rem;
  
}
.main-header {
  display:flex;
  justify-content:space-between;
  * {
    font-size:1.1rem;
    color:black;
  }
}
.main-container {
  display:flex;
  flex-wrap:wrap;
  padding-top:2rem;
  text-align:left;
  overflow:auto;
  &::scrollbar {
  
  display:none
  
  }
  
  > div {
    margin:1rem;
  }
  
}
.item {
  border:1px solid @bor-c;
  width:20rem;
  height:20rem;
  background-color:white;
  
}
.item-header {
  height:40%;
  position:relative;
  background-color:#ddd;
  padding:1rem;
  background-image:url(../../assets/images/class-bg-odd.jpg);
	background-repeat:no-repeat;
	background-size:cover;
  .top {
    display:flex;
    > div {
      &:first-child {
        flex:9;
        overflow: hidden;
        color:white;
        font-size:1.2rem;
      }
      &:last-child {
        flex:1;
      }
    }
  }
  .bottom {
    position:absolute;
    bottom:0;
    color:white;
  }
  
}
.item-main {
  padding:1rem;
  position:relative;
  height:60%;
  .homeworks {
    display:flex;
    flex-direction:column;
    margin-top:1rem;
  }
}
.class-info {
  width:90%;
  display:flex;
  justify-content:space-between;
  position:absolute;
  bottom:1rem;
}

/**item鼠标悬停**/

.item:hover {
  .item-header {
    a {
      text-decoration:underline;
    }
  }
  .homeworks {
    a {
      color:#2d8cf0;
    }
    
  }
  
}


</style>



