<template>
  <div>
    <div id = "header">
      <div id = "userIcon">
        <div class = "iconDeck">
          <hr/>
        </div>
        <Tooltip content="点击更换头像" placement="bottom">
        <div id = "icon">
          <div id = "icon-hint" @click="toggleShow">
          </div>
          <div id = "icon-display">
            <img :src = "icon"/>
          </div>
        </div>
        </Tooltip>
        <div class = "iconDeck">
          <hr/>
        </div>
      </div>
      <div id = "userInfo">
        <div>{{name}}</div>
        <div><span><Tooltip content="积分记录" placement="bottom"><Button type="text">当前积分</Button></Tooltip></span><span><Button type="text">开通课堂派vip</Button></span></div>
      </div>
      <div id = "nav"><span><Button type="text">个人设置</Button></span><span><Button type="text">通知设置</Button></span><span><Button type="text">我的文档</Button></span>
      </div>
    </div>
    <div id = "main">
      <transition name="component-fade" mode="out-in">
        <router-view>
        </router-view>
      </transition>
    </div>
    <div>
    <div class = "modalContainer">
        <div>
          <img-upload field="base64Img"
            @crop-success="cropSuccess"
            v-model="changeIconShow"
            :width="300"
            :height="300"
            :noRotate = "false"
            img-format="png">
          </img-upload>
        </div>
    </div>
    
    </div>
  </div>
</template>
<script>
import userInfo from '@/components/classes/setting_user'
import 'babel-polyfill'; // es6 shim
import Vue from 'vue';
import imgUpload from 'vue-image-crop-upload';
export default {
  name: 'classes-setting',
  data () {
    return {
      changeIconShow: false,
			imgDataUrl: '' // the datebase64 url of created image
    }
  },
  components: {
			'img-upload': imgUpload
	},
  created(){
    this.getUserInfo();
  },
  methods:{
    getUserInfo(){
      var that = this;
      this.$axios.get('getUserInfo.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.$store.commit('setUser',res.data.obj);
        }else{
        }
      })
      .catch(function(e){
      });
    },
    toggleShow() {
				this.changeIconShow = true;
		},
		cropSuccess(imgDataUrl, field){
      var that = this;
			this.imgDataUrl = imgDataUrl;
      var params = new URLSearchParams();
      params.append('base64Img',imgDataUrl);
      this.$axios.post('updateUserIcon.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.getUserInfo();
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
		}
  },
  computed:{
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    icon(){
      if(this.user == null){
        return '';
      }else{
        return this.user.icon;
      }
    }
  },
  userInfo:userInfo
}
</script>

<style scoped lang = "less">

@bor-c:#c8c8c8;


#header {
  background-color: rgba(255, 255, 255, 0.6);
  padding-top:3rem;
  border-top:1px solid @bor-c;
}
#userIcon{
  display:flex;
  .iconDeck {
    flex:8;
    padding-top:3rem;
  }
  #icon {
    flex:2;
    position:relative;
    #icon-hint,#icon-display {
      width:6rem;
      height:6rem;
      margin:0 auto;
      border-radius:100rem;
      }
    #icon-hint {
      position:absolute;
      top:0;
      right:0;
      left:0;
      border-radius:100rem;
      display:flex;
      align-items:center;
      justify-content:center;
      &:hover{
        background-color: rgba(255, 255, 255, 0.6);
        color:black;
        font-weight:bold;
        }
      }
    img {
      border-radius:100rem;
      width:inherit;
      height:inherit;
    }
  }
}
#userInfo {
  padding:1rem 0;
  > div:first-child {
    font-size:1.6rem;
    font-weight:bold;
  }
  > div+div {
    margin-top:.5rem;
    display:inline-block;
    background-color: #f2f2f2;
    padding:.5rem;
    border-radius:.2rem;
    > span+span {
      margin-left:4rem;
    }
  }

}
#nav {
  border-top:1px solid @bor-c;
  border-bottom:1px solid @bor-c;
  display:flex;
  justify-content:center;
  padding:1rem 0;
  > span {
    margin:0 3rem;
  }
}
#main {
  border:1px solid @bor-c;
  min-height:20rem;
  width:70%;
  margin:0 auto;
  margin-top:3rem;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2rem 4rem;

}
/**过渡效果**/
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
}
.component-fade-enter, .component-fade-leave-to
  /* .component-fade-leave-active for below version 2.1.8 */ {
    opacity: 0;
}
/*vue-cropper 效果*/

#cropperContainer {
  display: flex;
  
  
  #cropper {
    flex:5;
    overflow: hidden;
    height: 20rem;
    widows: 20rem;
    
  }
  #cropperPreview {
    flex:3;
  }
}

 

</style>



