<template>
  <div class = "checkin">
    <div class = "options">
      <div>
        <span><Button type = "primary" @click = "modal1=true">新建考勤</Button></span>
        <span style = "margin-left:2rem;">下载报表</span>
      </div>
      <div>
        <span style = "margin-right:2rem;">（  : 出勤  : 旷课  : 迟到  : 请假 ）</span>
        <span>
          <Input placeholder="学号，姓名" size = "small">
            <Button slot="append" icon="ios-search" size = "small"></Button>
          </Input>
        </span>
      </div>
    </div>
    <div class = "main">
      <Row type="flex">
        <Col span="6">
          <div class = "main-container main-left">
            <div style = "padding:0 1rem">
              <Dropdown>
                  <Button type = "text" style = "border:1px solid #c8c8c8">全部成员&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon></Button>
                  <DropdownMenu slot="list">
                      <DropdownItem><Button type = "text" size="small" disabled>分组1</Button></DropdownItem>
                      <DropdownItem><Button type = "text" size="small" >分组2</Button></DropdownItem>
                  </DropdownMenu>
              </Dropdown>
            </div>
            <div class = "student" v-for = "(item,index) in stus" :key="index">
              <div>{{item.sno}}</div>
              <div style = "font-weight:bold">{{item.username}}</div>
              <div class = "hovershow">
                <div>L 2 &nbsp;X 0</div>
                <div>P 2 &nbsp;X 0</div>
              </div>
            </div>
          </div>
        </Col>
        <Col span="18">
          <div class = "main-right">
            <div class = "main-container  main-right-container">
              <div>
                <div class = "checkin-info" v-for = "(item,index) in checkinNotices" :key="item.id">
                  <div>{{item.name}}</div>
                  <div class = "hoverhidden">{{$moment(item.createdatetime).format("YYYY/MM/DD hh:mm")}}</div>
                  <div class = "hovershow">
                    <Button type = "text" title = "编辑" @click = "editCheckinNotice(item)"><Icon type="edit"></Icon></Button>
                    <Button type = "text" title = "删除" @click = "deleteCheckinNotice(item)"><Icon type="trash-a"></Icon></Button>
                    <Button type = "text" title = "查看详情" @click = "overviewCheckin(index)"><Icon type="ios-search-strong"></Icon></Button>
                  </div>
                </div>
              </div>
              <div v-for = "(item,y) in stus" :key="y">
                <div @mouseover="setHover(x,y)" v-for = "(subitem,x) in item.checkins" :key="subitem.id"><Button type = "text" @click = "changeState(item.username,subitem)">{{checkinStateFilter(subitem.state)}}</Button></div>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
    <div>
      <Modal
        v-model="modal1"
        title="新建考勤"
        @on-ok="createCheckin"
        >
        <div>
          <Form>
            <FormItem label="考勤名称">
              <Input v-model = "model.name" placeholder="输入考勤名称"></Input>
            </FormItem>
          </Form>
          <div>
            <RadioGroup v-model = "model.type" size = "large">
                <Radio label="0">数字考勤</Radio>
                <Radio label="1">传统考勤</Radio>
            </RadioGroup>
          </div>
        </div>
      </Modal>
      <Modal
        v-model="modal2"
        :title="model.name"
        width = "40"
        ok-text = "结束"
        cancel-text = "放弃"
        :mask-closable = "false"
        @on-cancel = "cancelCheckin"
        @on-ok = "stopCheckin"
        >
        <div>
          <div style = "background:#ccc;display:flex;justify-content:space-around">
            <span style = "font-size:4rem">{{checkinCode[0]}}</span>
            <span style = "font-size:4rem">{{checkinCode[1]}}</span>
            <span style = "font-size:4rem">{{checkinCode[2]}}</span>
            <span style = "font-size:4rem">{{checkinCode[3]}}</span>
          </div>
          <div style = "display:flex;justify-content:space-between;margin-top:1rem">
            <div>学生通过以上数字完成签到（了解详情）</div>
            <div><Button type = "text" style = "color:blue">换一个</Button></div>
          </div>
          <div style = "min-height:10rem;display:flex;justify-content:center;align-items:center">
            <div>已签到</div>
            <div style = "font-size:6rem">
              <span style = "color:#ff0080;">{{onlineCheckinState.checked}}</span>
              <span>/{{onlineCheckinState.sum}}</span>
            </div>
          </div>
        </div>
      </Modal>

      <Modal
        v-model="modal4"
        title="重命名考勤"
        ok-text = "确定"
        cancel-text = "取消"
        @on-ok="editCheckinNoticeOK"
        >
        <div>
          <Form>
            <FormItem label="考勤名称">
              <Input v-model = "currentCheckinNoticeModel.name" placeholder="输入考勤名称"></Input>
            </FormItem>
          </Form>
        </div>
      </Modal>

      <Modal
        v-model="modal5"
        title="考勤总览"
        ok-text = "关闭"
        cancel-text = ""
        width = "50"
        >
        <div style = "text-align:center">
          <Menu mode="horizontal" active-name="1" @on-select = "checkinOverviewOnSelect" style = "display:flex;justify-content:center">
              <MenuItem name="1" style = "width:10rem">
                  出勤{{stusOverview.checked.length}}
              </MenuItem>
              <MenuItem name="0" style = "width:10rem">
                  旷课{{stusOverview.absent.length}}
              </MenuItem>
              <MenuItem name="2" style = "width:10rem">
                  迟到{{stusOverview.late.length}}
              </MenuItem>
              <MenuItem name="3" style = "width:10rem">
                  请假{{stusOverview.leave.length}}
              </MenuItem>
          </Menu>
          <div class = "checkinoverview-stus">
            <div v-show = "stusOverview.currentOverview.length > 0">
              <div v-for = "(item,index) in stusOverview.currentOverview" :key="index">
                <div class = "userinfo">
                  <span>{{index}}</span>
                  <span>{{item.sno}}</span>
                  <span style = "font-weight:bold">{{item.username}}</span>
                </div>
                <Button @click = "changeState(item.username,item)">{{checkinStateFilter(item.state)}}</Button>
              </div>
            </div>
            <div style = "padding-top:3rem;font-size:1.1rem" v-show = "stusOverview.currentOverview.length == 0">
              该状态下没有成员
            </div>
          </div>
        </div>
      </Modal>

      <Modal
        v-model="modal3"
        :title="'变更  '+ currentCheckinUserName + '  的考勤状态'"
        ok-text = "确定变更"
        cancel-text = "取消"
        @on-ok="changeStateOK"
        >
        <div style  = "display:flex;justify-content:center;">
          <RadioGroup v-model="currentCheckinState" type="button" size="large">
              <div>
                <Radio label="1" style = "margin:1.5rem;font-size:1.2rem;">出勤</Radio>
                <Radio label="0" style = "margin:1.5rem;font-size:1.2rem;">缺勤</Radio>
              </div>
              
              <div>
                <Radio label="2" style = "margin:1.5rem;font-size:1.2rem;">迟到</Radio>
                <Radio label="3" style = "margin:1.5rem;font-size:1.2rem;">请假</Radio>
              </div>
          </RadioGroup>
        </div>
      </Modal>

    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      modal1:false,
      modal2:false,
      modal3:false,
      modal4:false,
      modal5:false,
      hover:{
        x:0,
        y:0
      },
      checkinNotice:null,//当前数字考勤
      checkinNotices:[],
      stus:[],
      stusOverview:{
        checked:[],
        absent:[],
        late:[],
        leave:[],
        currentIndex:0,
        currentOverview:[]
      },
      currentCheckin:null,
      currentCheckinNotice:null,
      currentCheckinState:0,
      currentCheckinNoticeModel:{
        id:'',
        name:''
      },
      onlineCheckinState:{
        checked:0,
        sum:0
      },
      onlineCheckinStateIntevalId:'',
      model:{
        name:'',
        type:0,
      }
    }
  },
  created(){
    this.$store.commit('setCurrentNav','checkin');
    this.getCheckins();
  },
  methods:{
    setHover(x,y){
      this.hover.x = x;
      this.hover.y = y;
    },
    //新建考勤
    createCheckin(){
      if(this.model.type == 0){
        this.modal2 = true;
        this.onlineCheckinState.checked = 0;
        this.onlineCheckinState.sum = 0;
        this.onlineCheckinStateIntevalId = 
        setInterval(()=>{
        this.getCurrentCheckinState();
        },5000);
      }else if(this.model == 1){
      }
      var that = this;
      var params = new URLSearchParams(); 
      for(var key in this.model){
        params.append(key,this.model[key]);
      }
      params.append('classId',this.classId);
      this.$axios.post('tea/createCheckinNotice.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.checkinNotice = res.data.obj;
          that.getCheckins();
        }else{
          alert('出错了，请重试或重新登录');
        }
      })
      .catch((e)=>{
        console.log(e);
      });

    },
    //取消数字考勤
    cancelCheckin(){
      this.$Modal.confirm({
                    title: '放弃本次考勤？',
                    content: '',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.deleteCheckinNoticeOK(this.checkinNotice.id);
                      clearInterval(this.onlineCheckinStateIntevalId);
                    }
                });
    },
    //结束数字考勤
    stopCheckin(){
      var that = this;
      function stopCheckin(){
        that.$axios.get('tea/stopCheckin.aurora',{
          params:{
            id:that.checkinNotice.id
          }
        })
        .then((res)=>{
          if(res.data && res.data.success){
            that.$Modal.remove();
            that.$Message.success(res.data.msg);
          }else{
            alert('出错了，请重试或重新登录。');
          }
        })
        .catch((e)=>{
          console.log(e);
        });
      }
      this.$Modal.confirm({
                    title: '确认停止考勤',
                    content: '停止考勤后学生将不能再使用考勤码签到',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      stopCheckin();
                      this.getCheckins();
                      clearInterval(this.onlineCheckinStateIntevalId);
                    }
                });
    },
    //获得已签到的学生数
    getCurrentCheckinState(){
      var that = this;
      this.$axios.get('tea/getCurrentCheckinState.aurora',{
        params:{
          id:that.checkinNotice.id
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.onlineCheckinState.checked = res.data.obj;
        }else{
          alert('出错了，请重试或重新登录！');
        }
      })
      .catch((e)=>{
        console.log(e);

      });
    },
    //获取班级所有考勤状态
    getCheckins(){
      var that = this;
      this.$axios.get('tea/getCheckin.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
  
          that.checkinNotices = res.data.obj.checkinnotices;
          that.stus = res.data.obj.stucheckins;
        }
      })
      .catch((e)=>{
        console.log(e);

      });
    },
    //重命名考勤
    editCheckinNotice(item){
      this.modal4 = true;
      this.currentCheckinNotice = item;
      this.currentCheckinNoticeModel.name = item.name;
      this.currentCheckinNoticeModel.id = item.id;
    },
    editCheckinNoticeOK(){
      var that = this;
      var params = new URLSearchParams(); 
      for(var key in this.currentCheckinNoticeModel){
        params.append(key,this.currentCheckinNoticeModel[key]);
      }
      this.$axios.post('tea/renameCheckinNotice.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.modal4 = false;
          that.currentCheckinNotice.name = that.currentCheckinNoticeModel.name;
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });

    },
    //删除一条考勤通知
    deleteCheckinNotice(item){
      this.$Modal.confirm({
                    title: '确认删除该班级考勤？',
                    content: '',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.deleteCheckinNoticeOK(item.id);
                    }
                });
    },
    deleteCheckinNoticeOK(id){
      var that = this;
      this.$axios.get('tea/deleteCheckinNotice.aurora',{
        params:{
          id: id,
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.getCheckins();
          that.$Message.success(res.data.msg);
          that.$Modal.remove();
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    //打开考勤总览
    overviewCheckin(index){ //考勤总览
      this.stusOverview.currentIndex = index;
      this.overviewCheckinStateFilter();
      this.modal5 = true;
      //设置默认选项
      this.stusOverview.currentOverview = this.stusOverview.checked;
    },
    //考勤状态过滤器
    overviewCheckinStateFilter(){
      //重置stusOverview
      this.stusOverview.checked.splice(0,this.stusOverview.checked.length);
      this.stusOverview.absent.splice(0,this.stusOverview.absent.length);
      this.stusOverview.leave.splice(0,this.stusOverview.leave.length);
      this.stusOverview.late.splice(0,this.stusOverview.late.length);

      var index = this.stusOverview.currentIndex;

      for(var key in this.stus){
        var temp = this.stus[key].checkins[index];
        temp.username = this.stus[key].username;
        if(temp.state == 0){
          this.stusOverview.absent.push(temp);
        }else if(temp.state == 1){
          this.stusOverview.checked.push(temp);
        }else if(temp.state == 2){
          this.stusOverview.late.push(temp);
        }else if(temp.state == 3){
          this.stusOverview.leave.push(temp);
        }
      }



    },
    //考勤总览选中项
    checkinOverviewOnSelect(value){
      if(value == 0){
        this.stusOverview.currentOverview = this.stusOverview.absent;
      }else if(value == 1){
        this.stusOverview.currentOverview = this.stusOverview.checked;
      }else if(value == 2){
        this.stusOverview.currentOverview = this.stusOverview.late;
      }else if(value == 3){
        this.stusOverview.currentOverview = this.stusOverview.leave;
      }
    },
    //改变考勤状态
    changeState(name,item){
      this.currentCheckin = item;
      this.currentCheckin.name = name;
      this.modal3 = true;
    },
    changeStateOK(){
      var that = this;
      this.$axios.get('tea/changeCheckinState.aurora',{
        params:{
          id: that.currentCheckin.id,
          state: that.currentCheckinState
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.currentCheckin.state = that.currentCheckinState;
          that.modal3 = false;
          that.$Message.success(res.data.msg);
          that.currentCheckinState = 0;
          that.overviewCheckinStateFilter();
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
      
    },
    //考勤状态名称过滤器
    checkinStateFilter(state){
      if(state == 0){
        return '缺勤';
      }else if(state == 1){
        return '出勤';
      }else if(state == 2){
        return '迟到';
      }else if(state == 3){
        return '请假';
      }
    }
  },
  computed: {
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    checkinCode(){
      if(this.checkinNotice != null){
        var arr = [];
        for(var key in this.checkinNotice.checkincode){
          arr.push(this.checkinNotice.checkincode[key]);
        }
        this.onlineCheckinState.sum = this.checkinNotice.sum;
        return arr;
      }else{
        return [0,0,0,0]
      }
    },
    currentCheckinUserName(){
      if(this.currentCheckin == null){
        return '';
      }else{
        return this.currentCheckin.name;
      }
    }
  },
  watch: {
    modal1(){
      if(this.modal1 == true){
        this.model.name = this.$moment(new Date()).format("YYYY.MM.DD");
      }
    }
  }
}
</script>

<style lang = "less" scoped>
  @bor-c:#c8c8c8;
  .checkin {
    background: white;
    min-height: inherit;
    padding:2rem;
    .options {
      display: flex;
      justify-content: space-between;
      > div {
        display: flex;
        align-items: center;
      }
    }
    .main {
      margin-top:2rem;
      border:1px solid @bor-c;
      text-align: center;
      .main-left {
        border-right:1px solid @bor-c;
        > div {
          display: flex;
          justify-content: flex-start;
          padding-right:1rem;
          align-items: center;
          border-bottom:1px solid @bor-c;
          height: 4rem;
        }
      }
      .main-right {
        overflow:auto;
        .main-right-container {
          display:inline-block;
	        min-width:100%;
          > div {
            height: 4rem;
            border-bottom: 1px solid @bor-c;
            display: flex;
            > div {
              width:10rem;
              display: flex;
              justify-content: center;
              align-items:center;
              flex-direction: column;
            }
          }
        }
      }
      .main-container {
        min-height:40rem;
        > div {
          &:nth-child(odd){
            background-color:#eee;
          }
          &:hover {
           /*background-color:#eee;*/
          }
        }
      }
    /**具体样式**/
      /*左边每行*/
      .student{
        padding-right: 1rem;
        position: relative;
        > div {
          margin-left:1rem;
          
        }
        .hovershow {
            display:none;
            position: absolute;
            right:1rem;
            
          }
        &:hover {
          .hovershow {
            display:block;
          }
        }
      }
      /*右边第一行*/
      .checkin-info {
        .hovershow {
          display:none;
          > * {
            background:#ddd;
            padding:.5rem;
            border-radius: 3px;
            &:hover {
              background:#ccc;
            }
          }
        }
        .hoverhidden {
          color: #aaa;
        }
        &:hover {
          .hovershow {
            display:block;
          }
          .hoverhidden {
            display: none;
          }
        }

      }
      
    }
    
  }
  /*考勤总览对话框，学生列表*/
  .checkinoverview-stus {
    min-height:20rem;
    margin-top:1rem;
    > div > div {
      font-size: 1.1rem;
      border:1px solid @bor-c;
      height:4rem;
      padding:0 1rem;
      display: flex;
      justify-content:space-between;
      align-items: center;
      + div {
        border-top:none;
      }
      &:nth-child(odd){
        background-color:#eee;
      }
      &:hover {
        background-color:#ddd;
      }
      .userinfo span {
        margin-right:2rem;

      }
    }

  }
</style>



