<template>
  <div class = "header">
    <div style = "flex:2;color:white;">
      <router-link to = "/classroom/topic">
      <Icon type="chevron-left"></Icon>&nbsp;&nbsp;{{clazz.name}}
      </router-link>
    </div>
    <div style = "flex:8">
      <nav>
        <a href = "javascript:void(0)" ><span @click = "to('comment-details')" :class = "{'nav-selected':navSelected == 'comment-details'}">话题详情</span></a>
        <a href = "javascript:void(0)" ><span @click = "to('comment-stats')" :class = "{'nav-selected':navSelected == 'comment-stats'}">话题参与统计</span></a>
      </nav>
    </div>
    <div class = "user-option" style = "flex:2">
      <span><router-link to="/login"><Badge :value="200" :max="99" class="item"><Icon type="ios-bell-outline" size = "28"></Icon></Badge></router-link></span>
      <span>
        <Dropdown>
                <span>
                  <Avatar :src = "icon" size = "large" :title = "name"/>
                </span>
                <DropdownMenu slot="list">
                  <DropdownItem><router-link to="/classes/setting"><Button type="text" size="small"><Icon type="android-settings"></Icon>个人设置</Button></router-link></DropdownItem>
                  <DropdownItem><Button type="text" size="small"><Icon type="android-download"></Icon>文库下载</Button></DropdownItem>
                  <DropdownItem><Button type="text" size="small" @click="logout"><Icon type="log-out"></Icon>退出登录</Button></DropdownItem>
                </DropdownMenu>
        </Dropdown>
      </span>
    </div>
  </div>
</template>
<script>
export default({
  data(){
    return {
    }
  },
  methods:{
    to(targ){
      this.headerShow = true;
      this.$router.push('/topic-comment/' + this.$route.params.id + '/' +targ);
    },
    logout(){
      var that = this;
      this.$axios.get('logout')
      .then(function(res){
        if(res.data.success == true){
          that.$router.push('/login');
        }
      })
      .catch(function(e){
        console.log(e);
      });
    }
  },
  computed: {
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    icon(){
      if(this.user == null){
        return '';
      }else{
        return this.user.icon;
      }
    },
    clazz(){return this.$store.getters.getCurrentClass},
    navSelected(){
      return this.$store.getters.getCurrentNav;
    }
  }
})
</script>
<style lang = "less" scoped>
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding:0 2rem;
  background: #b13e0d;
  nav {
    height: 4rem;
    z-index:1;
    color:#eeedee;
    font-size:1.1rem;
    min-height:4rem;
    text-align:center;
    span:nth-child(n) {
      display:inline-block;
      height:inherit;
      width:8rem;
      margin:0 .5rem;
      line-height:3.8rem;
      box-sizing:border-box;
      -moz-box-sizing:border-box; /* Firefox */
      -webkit-box-sizing:border-box; /* Safari */
      &:hover {
          border-bottom:1px solid white;
        }
      }
  }
  .nav-selected {
    border-bottom:1px solid white;
  }
  .user-option {
      padding:0 2rem;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      > span {
        margin-left:3rem;
      }
    }
}

</style>


