<template>
  <div id = "topic">
    <div class = "edit-topic">
      <a href = "javascript:void(0)" @click = "onEdit = true" v-show="!onEdit">
        <div class = "edit-start">
          <Icon type="edit"></Icon>&nbsp;
          <span>发起新话题</span>
        </div>
      </a>
      <div class = "edit-area" v-show="onEdit">
        <div>
          <Input  placeholder="输入话题名称" v-model = "topicModel.name" size = "large"></Input>
        </div>
        <div>
          <div id="topiceditor"></div>
        </div>
        <div class = "edit-area-footer">
          <div><Button >导入话题</Button></div>
          <div><Button @click = "onEdit = false">取消</Button><Button type = "primary" @click = "editOK">发布话题</Button></div>
        </div>
      </div>

    </div>
    <div class = "topic-list">
      <div class = "topic-none-default" v-if = "noTopics && !onEdit">
        <div>
          <p style = "font-size:1.2rem;font-weight:bold">点击上方按钮，您可以发起新话题。</p>
          <p>发起话题之后，无论是老师还是同学们，都可以基于此话题进行讨论</p>
        </div>
      </div>
      <div class = "topic-list-option" v-if = "!noTopics">
        <div><span><Button type = "text">全部</Button></span><span><Button type = "text">精化</Button></span></div>
        <div>
          <div v-if = "userType == 1"><Button type = "primary">话题参与情况</Button></div>
        </div>
      </div>
      <div class = "list" v-if = "!noTopics">
        <div v-for = "item in topicNotices" :key = "item.id">
          <div class = "item-main" v-show="!item.onEdit">
            <div class = "item-main-left">
              <div><router-link :to = "'/topic-comment/' + item.id + '/comment-details'"><h2>{{item.name}}</h2></router-link></div>
              <div style = "color:#aaa;"><span>楼主: {{item.author}}</span>&nbsp;&nbsp;<span>时间：{{$moment(new Date(item.createdatetime)).format('LLLL')}}</span></div>
              <div>
                <div v-html="item.content"></div>
              </div>
              <div><router-link :to = "'/topic-comment/' + item.id + '/comment-details'">{{item.commentsum}}&nbsp;条评论</router-link></div>
            </div>
            <div class = "item-main-right">
              <div v-if = "userType == 1">
                <Dropdown trigger="click">
                  <a href="javascript:void(0)">
                    <span><Icon type="ios-keypad"></Icon></span>
                  </a>
                  <DropdownMenu slot="list" style = "text-align:center">
                      <DropdownItem><Button type = "text">设为置顶</Button></DropdownItem>
                      <DropdownItem><Button type = "text">设为精化</Button></DropdownItem>
                      <DropdownItem><Button type = "text">保存到...</Button></DropdownItem>
                      <DropdownItem><Button type = "text" @click = "redit(item)">编辑</Button></DropdownItem>
                      <DropdownItem><Button type = "text" @click = "deleteTopic(item)">删除</Button></DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </div>
            </div>
          </div>
          <div class = "edit-area item-editor" v-show = "item.onEdit">
            <div>
              <Input v-model = "item.name" placeholder="输入话题名称" size = "large"></Input>
            </div>
            <div>
              <div :id="'topicEditor-' + item.id"></div>
            </div>
            <div class = "edit-area-footer">
              <div></div>
              <div><Button @click = "reditCancel(item)">取消</Button><Button type = "primary"@click = "reditOK(item)">保存编辑</Button></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default({
  name:'classroom-topic',
  data(){
    return {
      topicNotices:[],
      topiceditor:null,
      onEdit:false,
      toolbars: [
        [
          'undo', 'redo', '|',
          'bold', 'italic', 'underline', 'strikethrough', 'superscript', 'subscript','blockquote','touppercase', 'tolowercase','link',"|",
          'forecolor', 'backcolor',"|",
          'fontfamily', 'fontsize', '|',
          'justifyleft', 'justifycenter', 'justifyright',"|",
          'insertcode',
          'spechars','simpleupload','imageleft', 'imageright', 'imagecenter',"|",
          'attachment','horizontal','preview',
          'fullscreen' 
        ]
      ],
      topicModel:{
        name:'',
        content:'',
      }
    }
  },
  created(){
    this.$store.commit('setCurrentNav','topic');
    this.getTopicNotice();
    
  },
  methods:{
   
    editOK(){
      var that = this;
      this.topicModel.content = this.topiceditor.getContent();
      var params =  new URLSearchParams(); 
      params.append('name',this.topicModel.name);
      params.append('content',this.topicModel.content);
      params.append('classId',this.classId);
      console.log(this.topicModel);
      this.$axios.post('createTopicnotice.aurora', params)
      .then(function(res){
        if(res.data && res.data.success){
          var temp = res.data.obj;
          that.onEdit = false;
          that.$Message.success(res.data.msg);
          that.$emit('headershow');
          temp.onEdit = false;
          that.topicNotices.unshift(temp);
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    getTopicNotice(){
      var that = this;
      this.$axios.get('getTopicnotice.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          var temp = res.data.obj;
          for(var key in temp){
            temp[key].onEdit = false;
          }
          that.topicNotices = temp;
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    redit(item){
      this.$emit('headerhidden');
      item.onEdit = true;
      UE.delEditor('topicEditor-' + item.id);
      item.editor = UE.getEditor('topicEditor-' + item.id,{toolbars:this.toolbars});
      item.editor.ready(function(){
        this.setContent(item.content);
      });
    },
    reditOK(item){
      var that = this;
      var content = item.editor.getContent();
      var params =  new URLSearchParams(); 
      params.append('id',item.id);
      params.append('name',item.name);
      params.append('content',content);
      params.append('classId',this.classId);
      this.$axios.post('tea/updateTopicnotice.aurora', params)
      .then(function(res){
        if(res.data && res.data.success){
          item.onEdit = false;
          that.$Message.success(res.data.msg);
          that.$emit('headershow');
          UE.delEditor('topicEditor-' + item.id);
          item.content = content;
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    reditCancel(item){
      item.onEdit = false;
      this.$emit('headershow');
      if(item.editor){
        item.editor.destroy();
      }
    },
    deleteTopic(item){
      this.$Modal.confirm({
                    title: '确认删除该话题？',
                    content: '<p>所有相关评论都将被删除</p>',
                    loading: true,
                    onOk: () => {
                      this.deleteTopicOK(item.id);
                    }
                });
    },
    deleteTopicOK(id){
      var that = this;
      this.$axios.get('tea/deleteTopicNotice.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          for(var i = 0; i < that.topicNotices.length; i++){
            if(that.topicNotices[i].id == id)
              that.topicNotices.splice(i,1);
          }
          that.$Modal.remove();
          that.$Message.info(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e)
      });
    }
  },
  computed:{
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    noTopics(){
      return this.topicNotices.length == 0
    },
    userType(){
      return this.$store.getters.getType;
    }
  },
  watch:{
    onEdit(){
      if(this.onEdit == false){
        this.$emit('headershow');
        this.topiceditor.execCommand('cleardoc');
        UE.delEditor('topiceditor');
      }else{
        this.$emit('headerhidden');
        UE.delEditor('topiceditor');
        this.topiceditor = UE.getEditor('topiceditor',{toolbars:this.toolbars});
      }
    },
    classId(){
      this.getTopicNotice();
    }
  }
  
})
</script>
<style lang = "less" scoped>
@bor-c:#c8c8c8;
  #topic {
     padding-top:1.5rem;
  }
  .edit-topic {
    background-color:white;
    .edit-start {
      padding:1rem 2rem;
      border:1px solid @bor-c;
    }
  }
  /*所有编辑区样式*/
  .edit-area {
      border:1px solid @bor-c;
      padding:1rem;
      > div+div {
        margin-top:1rem;
      }
      .edit-area-footer {
        display:flex;
        justify-content:space-between;
        background-color:#ddd;
        padding: 2rem;
        > div+div > * {
          margin-left: 1rem;
        }
      }
    }
  
  .topic-list {
    margin-top:2rem;
    > div + div {
      margin-top:1rem;
    }

    .topic-none-default {
      text-align: center;
      height: 8rem;
      background-color:rgba(0,0,0,.03);
      display: flex;
      justify-content:center;
      align-items:center;
    }
    .topic-list-option {
      padding:0 .5rem;
      display: flex;
      justify-content:space-between;
      span+span {
        margin-left:1.5rem;
      }
    }
    .list {
      margin-bottom:2rem;
      > div + div {
        margin-top:1.5rem;
      }
      .item-main {
        background-color:white;
        display: flex;
        padding:2rem;
        padding-bottom:.5rem;
        border:1px solid @bor-c;
        box-shadow:1px 1px 2px 1px #c8c8c8;
        .item-main-left {
          flex:9;
          max-width:100%;
          overflow: hidden;
          > div {
            + div {
              margin-top:1.2rem;
            }
          }
        }
        .item-main-right {
          flex:1;
          text-align: right;
          
        }
      }
      .item-editor {
        background-color:white;
      }
    }
  }

</style>


