<template>
  <div id = "homework">
    <div id = "addhomework" v-if = "userType == 1">
      <Button icon = "plus-round" :type = "homeworkModel.type == 0 ? 'primary':'default'" @click = "addHomework(0)">添加个人作业</Button>
      <Button icon = "plus-round" :type = "homeworkModel.type == 1 ? 'primary':'default'" @click = "addHomework(1)">添加小组作业</Button>
    </div>

    <div class = "edithomework" v-if = "userType == 1" v-show = "editorIsshow">
      <div>
        <Input v-model="homeworkModel.title" placeholder="输入作业名称" size = "large"></Input>
      </div>
      <div>
        <div id="editor"></div>
      </div>
      <div class = "edithomeworkInfo">
        <span>截止时间：<DatePicker  :options="disabledDate" v-model = "homeworkModel.cutoffdatetime" type="datetime"  placeholder="选择截止时间" style="width: 200px"></DatePicker></span>
        <span>满分值:&nbsp;&nbsp;<InputNumber :max="999" :min="0" v-model = "homeworkModel.fullscore"></InputNumber></span>
      </div>
      <div class = "edithomeworkdone">
        <div><Button >导入作业</Button></div>
        <div><Button @click = "editCancel">取消</Button><Button type = "primary" @click = "editOK">发布作业</Button></div>
      </div>
    </div>

    <div id = "homeworklist">
      <div class = "homework-none-default" v-if = "noHomeworks && !editorIsshow">
        <div>
          <p style = "font-size:1.2rem;font-weight:bold" v-if= "userType == 1">点击上方按钮，您可以添加新的班级作业。</p>
          <p style = "font-size:1.2rem;font-weight:bold" v-if= "userType == 0">您的老师暂未发布任何作业。</p>
          <p v-if= "userType == 1">快速发布、收集学生作业，支持50多种文档在线批阅。多维度（全班查重、作业字数等）统计学生作业情况。</p>
        </div>
      </div>
      <div class = "item" v-for = "item in homeworkNotices" :key = "item.id">
        <div v-show = "!item.onEdit">
          <div class = "item-header">
            <div class = "startime"><span>{{item.type==0?'个人作业':'小组作业'}}</span><span>{{$moment(new Date(item.createdatetime)).format('LLLL')}}</span></div>
            <div>
              <Dropdown trigger="click" style="margin-left: 20px" v-if = "userType == 1">
                <a href="javascript:void(0)">
                  <span><Icon type="ios-keypad"></Icon></span>
                </a>
                <DropdownMenu slot="list">
                    <DropdownItem><Button type = "text">保存到</Button></DropdownItem>
                    <DropdownItem><Button type = "text" @click = "redit(item)">编辑</Button></DropdownItem>
                    <DropdownItem><Button type = "text" @click = "delHn(item.id)">删除</Button></DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </div>
          </div>
          <div class = "item-main">
            <div class = "item-main-left">
              <h2>{{item.title}}</h2>
              <div class = "item-main-left-container" v-html="item.content">
              </div>
            </div>
            <div class = "item-main-right-tea" v-if = "userType == 1">
              <div style = "color:rgba(0,200,0,1)">
                <div class = "total">{{item.reviewed}}</div>
                <div>已批</div>
              </div>
              <div @click = "$router.push('/homeworkview/'+item.id)">
                <div class = "total">{{item.unreviewed}}</div>
                <div>未批</div>
              </div>
              <div style = "color:red">
                <div class = "total">{{item.unsubmit}}</div>
                <div>未交</div>
              </div>
            </div>
            <div class = "item-main-right-stu" v-if = "userType == 0">
                <Button type = "primary" @click = "toSubmithomework(item)">{{textFilter(item)}}</Button>
            </div>
          </div>
          <div class = "item-footer">
            <span>截止日期：{{$moment(new Date(item.cutoffdatetime)).format('LLLL')}}</span><span>*条讨论</span>
          </div>
        </div>
        <div class = "edithomework" v-show = "item.onEdit">
          <div>
            <Input v-model="item.title" placeholder="输入作业名称" size = "large"></Input>
          </div>
          <div>
            <div :id="item.id"></div>
          </div>
          <div class = "edithomeworkInfo">
            <span>截止时间：<DatePicker  required v-model = "item.cutoffdatetime" type="datetime"  placeholder="选择截止时间" style="width: 200px"></DatePicker></span>
            <span>满分值:&nbsp;&nbsp;<InputNumber :max="999" :min="0" v-model = "item.fullscore"></InputNumber></span>
          </div>
          <div class = "edithomeworkdone">
            <div></div>
            <div><Button @click = "reditCancel(item)">取消</Button><Button type = "primary" @click = "reditOK(item)">保存</Button></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default({
  name:'homework',
  data(){
    return {
      homeworkNotices:[],
      editor:{},
      editorIsshow:false,
      homeworkModel:{
        title:'',
        content:'',
        type:0,
        cutoffdatetime:'',
        fullescore:0,
        classId:'',
      },
      disabledDate: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000;
        }
      },
      toolbars: [
        [
          'undo', 'redo', '|',
          'bold', 'italic', 'underline', 'strikethrough', 'superscript', 'subscript','blockquote','touppercase', 'tolowercase','link',"|",
          'forecolor', 'backcolor',"|",
          'fontfamily', 'fontsize', '|',
          'justifyleft', 'justifycenter', 'justifyright',"|",
          'insertcode',
          'spechars','simpleupload','imageleft', 'imageright', 'imagecenter',"|",
          'attachment','horizontal','preview',
          'fullscreen' 
        ]
      ]
    }
  },
  created(){
    this.$store.commit('setCurrentNav','homework');
    this.homeworkModel.classId = this.classId;
    this.getHomeworknotice();
  },
  methods:{
    addHomework(type){
      this.homeworkModel.type = type;
      if(!this.editorIsshow){
        this.$emit('headerhidden');
        this.editorIsshow = true;
        UE.delEditor('editor');//非常重要，删除已经实例化的编辑器
        this.editor = UE.getEditor('editor',{toolbars:this.toolbars});
      }
    },
    editOK(){
      var params =  new URLSearchParams();//Using application/x-www-form-urlencoded format  
      this.homeworkModel.classId = this.classId;
      this.homeworkModel.content = this.editor.getContent();
      for(var key in this.homeworkModel){
        params.append(key,this.homeworkModel[key]);
      }
      var that = this;
      this.$axios.post('tea/createHomeworknotice.aurora',params)
      .then(function(res){
        if(res.data && res.data.success){
          that.editorIsshow = false;
          var temp = res.data.obj;
          temp.onEdit = false;//添加属性
          that.homeworkNotices.unshift(temp);
          that.$emit('headershow');
        }
      })
      .catch(function(e){
        console.log(e);
      });
      
    },
    editCancel(){
      this.editorIsshow = false;
      this.$emit('headershow');
      this.editor.destroy();

    },
    getHomeworknotice(){
      var that = this;
      this.$axios.get('getHomeworknotices.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          var hns = res.data.obj;
          for(var key in hns){
            hns[key].onEdit = false;
            hns[key].editor = {};
          }
          that.homeworkNotices = hns;
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    redit(item){
      this.$emit('headerhidden');
      item.onEdit = true;
      UE.delEditor(item.id);//非常重要，删除已经实例化的编辑器
      item.editor = UE.getEditor(item.id,{toolbars:this.toolbars});
      item.editor.ready(function(){
        this.setContent(item.content);
      });
      
    },
    reditOK(item){
      item.content = item.editor.getContent();
      var params =  new URLSearchParams();//Using application/x-www-form-urlencoded format  
      params.append('id',item['id']);
      params.append('title',item['title']);
      params.append('content',item['content']);
      params.append('fullscore',item['fullscore']);
      
      var that = this;
      this.$axios.post('tea/updateHomeworknotice.aurora',params)
      .then(function(res){
        if(res.data && res.data.success){
          item.onEdit = false;
          item.editor.destroy();
          that.$emit('headershow');
        }
      })
      .catch(function(e){
        console.log(e);
      });
    },
    reditCancel(item){
      item.onEdit = false;
      item.editor.destroy();
      this.$emit('headershow');

    },
    delHn(id){
      this.$Modal.confirm({
                    title: '确认删除该作业通知？',
                    content: '<p>该作业通知的所有相关信息都将被删除</p>',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.delHnOK(id);
                    }
                });
    },
    delHnOK(id){
      var that = this;
      this.$axios.get('tea/deleteHomeworknotice.aurora',{
        params:{
          id:id
        }
      })
      .then(function(res){
        if(res.data && res.data.success){
          for(var i = 0; i < that.homeworkNotices.length; i++){
            if(that.homeworkNotices[i].id == id)
              that.homeworkNotices.splice(i,1);
          }
          that.$Modal.remove();
          that.$Message.info(res.data.msg);
        }
      })
      .catch(function(){

      });
    },
    //销毁已经实例化的所有编辑器
    destoryEditors(){
      if(typeof(UE.getEditor('editor')) !='undefined'){
          UE.getEditor('editor').destroy();
        }
      for(var key in this.homeworkNotices){
        if(typeof(UE.getEditor(this.homeworkNotices[key].id)) !='undefined'){
          UE.getEditor(this.homeworkNotices[key].id).destroy();
        }
      }
    },
    toSubmithomework(item){
      this.$store.commit('setCurrentObject',item);
      this.$router.push('/classroom/submithomework');
    },
    //filter
    textFilter(item){
      if(item.homeworkstate == 0){
        return '提交作业';
      }else if(item.homeworkstate == 1){
        return '更新提交';
      }else if(item.homeworkstate == 2){
        return '查看成绩'
      }else if(item.homeworkstate == 3)
      return '需要重交'
    }
  },
  computed:{
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    userType(){
      return this.$store.getters.getType;
    },
    noHomeworks(){
      return this.homeworkNotices.length == 0
    },
  },
  watch:{
    editorIsshow(){
      if(this.editorIsshow == true){
        this.homeworkModel.cutoffdatetime = Date.now();
      }else {
        this.editor.setContent('');
        this.editor.destroy();
        UE.delEditor('editor');
      }
    },
    classId(){
      this.getHomeworknotice();
    }
  }
})
</script>

<style lang = "less" scoped >
@bor-c:#c8c8c8;

#homework {
  padding-top:1rem;

}
#addhomework {
  padding:1rem 0;
}
.edithomework {
  background-color:#FCF8F1;
  border:1px solid #aaa;
  padding:1rem;
  > div+div {
    margin-top:1rem;

  }
}
.edithomeworkInfo {
  padding:1rem;
}
.edithomeworkdone {
  display:flex;
  justify-content:space-between;
  background-color:#ddd;
  padding: 2rem;
  > div+div > * {
    margin-left: 1rem;
  }
}

#homeworklist {
  margin-top:1.5rem;
  margin-bottom:2rem;
  .homework-none-default {
    text-align: center;
    height: 8rem;
    background-color:rgba(0,0,0,.03);
    display: flex;
    justify-content:center;
    align-items:center;
  }
  .item {
    margin-top: 1.5rem;
    border:1px solid #c8c8c8;
    padding:2rem;
    padding-bottom:1rem;
    background-color:white;
    box-shadow:1px 1px 2px 1px #c8c8c8;
    > div {
     
    }
    .item-header {
      display: flex;
      justify-content: space-between;
      .startime {
        color:#aaa;
        > span + span {
          margin-left:3rem;
        }
      }
    }
    .item-main {
      display: flex;
      margin-top:2rem;
      .item-main-left {
        min-height: 5rem;
        flex:8;
        max-width:100%;
        overflow: hidden;
      }
      .item-main-left-container {
        padding:2rem 0;
      }
      .item-main-right-tea {
        flex:2;
        display: flex;
        justify-content: center;
        max-height: 5rem;
        > div {
          border-left:1px solid green;
          text-align: center;
          &:hover {
            cursor:pointer;
            background-color:antiquewhite;
          }
          > div {
            margin:0 1rem;
          }
          .total {
            font-size:2rem;
          }
        }
      }
      .item-main-right-stu {
        flex:2;
        text-align:center;
      }

    }
    .item-footer {
      display: flex;
      > span {
        padding:.4rem;
        &:first-child {
          background-color: #ddd;
          margin-right: 2rem;
        }

      }

    }

  }





}

</style>


