<template>
  <div class = "login-pan">
    <div class = "flex type">
	  <span v-bind:class="{'onselected':type == 0}"><a href = "javascript:void(0)" @click = "changetype(0)">普通登录</a></span>
	  <span v-bind:class="{'onselected':type == 1}"><a href = "javascript:void(0)" @click = "changetype(1)">手机短信登录</a></span>
    </div>
	<transition name="fade">
	<div v-show = "type == 0" v-bind:key = "type">
	  
    <Form :model="form1" label-position="right" :label-width="60" :rules="rules" ref = "form1">
        <FormItem label="账号" :required = "true" prop="identity">
            <Input v-model="form1.identity"><Icon type="ios-person-outline" slot="prepend"></Icon></Input>
        </FormItem>
        <FormItem label="密码" :required = "true" prop = "pwd" >
            <Input v-model="form1.pwd" type="password"><Icon type="ios-locked-outline" slot="prepend"></Icon></Input>
        </FormItem>
        <FormItem label="验证码" :required = "true">
            <Input v-model="form1.verifycode" type="text" @on-enter = "login"><Icon type="alert-circled" slot="prepend"></Icon></Input>
        </FormItem>
    </Form>

    <div class = "reg-captcha" style = "text-align:right;">
      <img :src = "phoneCaptcha" title = "点击更换验证码" @click = "getVerifyCode" style = "width:12rem;height:4rem;border-radius:5px;">
    </div>

  </div>
	</transition>
  
	<transition name="fade">
	<div v-show = "type == 1" v-bind:key = "type">
    <Form :model="form1" label-position="right" :label-width="60" :rules="rules">
        <FormItem label="手机号" prop = "identity" :required = "true">
            <Input v-model="form1.identity"><Icon type="ios-person-outline" slot="prepend"></Icon></Input>
        </FormItem>
        <FormItem label="验证码">
            <Input v-model="form1.pwd"><Icon type="alert-circled" slot="prepend"></Icon></Input>
        </FormItem>
       <FormItem style = "text-align:right;">
         <Button type = "primary">获取短信验证码</Button>
       </FormItem>
    </Form>
  </div>
	</transition>
	
	<div class = "flex" id = "option"><span><Checkbox v-model="checked">下次自动登录</Checkbox></span> <span>忘记密码?</span></div>
	
  <div style = "color:red;">{{msg}}</div>
	
  <div class = "flex" id = "button2"><Button type="primary" @click = "login">登录</Button></div>
	
  </div>
</template>

<script>
export default {
  name: 'header',
  data () {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.form1.pass !== '') {
            
          }
          callback();
        }
      };

      var validateIdentity = (rule, value, callback) => {
        var rePhone = /^1\d{10}$/ ;
        var reEmail = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/ ;
        this.msg = '';
        if (value === '') {
          callback(new Error('请输入账号'));
        } else {
          if(reEmail.test(value)){
            this.form1.email = value;
            callback();
          }else if(rePhone.test(value)){
            this.form1.phone = value;
            callback();
          }else{
            callback(new Error('电话或邮箱错误'));
          }
        }
      };
    return {
      type:0,
      checked:'',
      input:'',
      msg:'',
      phoneCaptcha:'',
      form1:{
        identity:'',
        phone:'',
        email:'',
        pwd:'',
        verifycode:''
      },
      rules: {
            pwd: [
              { validator: validatePass, trigger: 'blur' }
            ],
            identity: [
              { validator: validateIdentity, trigger: 'blur' }
            ],
          }
    }
  },
  created () {
    this.getVerifyCode();
  },
  methods:{
    changetype:function(type){
      this.type = type;
    },
    login:function(){
      var that = this;
      var searchparams = new URLSearchParams();//Using application/x-www-form-urlencoded format  
      for(var prop in this.form1){
        searchparams.append(prop,this.form1[prop]);
      }
      this.$axios.post('log.aurora',searchparams)
      .then(function(res){
        if(res.data && res.data.success){
          that.$store.commit('setType',res.data.obj);
          that.$router.push('/classes/main');
        }else{
          that.getVerifyCode();
          that.msg = res.data.msg;
          that.form1.verifycode = '';
        }
      })
      .catch(function(e){
        
      });
      
    },
    getVerifyCode(){
      var that = this;
      this.$axios.get('getVerifyCode.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.phoneCaptcha = res.data.obj;
        }else{
         
        }
      })
      .catch(function(e){
        console.log(e);
      })
    }
  },
  computed:{
    
  }
}
</script>

<style scoped lang = "less">
@bor-c:#c8c8c8;

.login-pan {
  padding:1rem;
  border:1px solid @bor-c;
  min-width:28rem;
  background-color:rgba(255, 251, 240, .7);
  > div > div{
   margin:.8rem 0;
  }
}
.flex {
  display:flex;
  align-items:center;
  label {
    flex:1;
  }
  label+* {
    flex:5;
  }
}
.type {
  padding:1rem 0;
  > span {
    flex:1;
	&:hover {
	  color:blue;
	}
  }
  > span+span {
    flex:1;
	border-left:1px solid @bor-c;
  }

}
#button1 {
  text-align:right;
}
#button2 {
  margin:.5rem 0;
  > * {
    flex:1;
  }
}
#option {
  justify-content:space-between;
  margin:.5rem 0;
  padding:0 .5rem;
}
.onselected {
  color:blue;
}

/**过渡效果**/

.fade-enter-active, .fade-leave-active {
  transition: opacity 1s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}

</style>




