<template>
  <div id="userSetting">
    <div class ="module">
      <div class = "title" >基本资料</div>
      <div class = "info" v-if = "!basicInfoOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>姓名:</label></Col><Col span="8">{{name}}</Col><Col span="2" offset="12"><Button type="ghost" @click = "basicInfoOnEdit=true">编辑资料</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>学校:</label></Col><Col span="8">{{school}}</Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>学号:</label></Col><Col span="8">{{sno}}</Col><Col span="2" offset="12"></Col>
        </Row>
      </div>
      <div class = "info" v-if = "basicInfoOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>姓名:</label></Col><Col span="8"><Input v-model="basicInfoModel.name" placeholder="请输入姓名"></Input></Col><Col span="2" offset="12"><Button type="ghost" @click = "basicInfoOnEdit=false">放弃编辑</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>学校:</label></Col><Col span="8"><Input v-model="basicInfoModel.school" placeholder="请输入学校"></Input></Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>学号:</label></Col><Col span="8"><Input v-model="basicInfoModel.sno" placeholder="请输入学号"></Input></Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><Button type="primary" size = "small" @click = "updateUserInfo">保存</Button></Col><Col span="8"></Col><Col span="2" offset="12"></Col>
        </Row>
      </div>
    </div>
    <div class ="module">
      <div class = "title" >账号设置</div>
      <div class = "info" v-if = "!basicInfoOnEdit && !emailOnEdit && !phoneOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>账号:</label></Col><Col span="8">{{account}}</Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>邮箱:</label></Col><Col span="8">{{email==''?'未绑定':email}}</Col><Col span="2" offset="12"><Button type="ghost" @click = "emailOnEdit=true">{{email==''?'绑定邮箱':'修改邮箱'}}</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>手机:</label></Col><Col span="8">{{phone}}</Col><Col span="2" offset="12"><Button type="ghost" @click = "editPhone">修改手机</Button></Col>
        </Row>
      </div>
      
      <div class = "info" v-if = "emailOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>当前邮箱:</label></Col><Col span="8">{{email==''?'未绑定':email}} &nbsp;<Button type="ghost" @click = "modalVisible1 = true" v-if = "email!=''">解绑</Button></Col><Col span="2" offset="12"><Button type="ghost" @click = "emailOnEdit=false">放弃编辑</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>新邮箱:</label></Col>
          <Col span="8"><Input v-model="emailModel.email" placeholder="请输入新邮箱" @on-blur = "checkEmail"></Input></Col>&nbsp;&nbsp;
          <Col class= "error">{{errorMsg.emailErrMsg}}</Col>
          <Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>密码校验:</label></Col><Col span="8"><Input v-model="emailModel.pwd" type = "password" placeholder="请输入登录密码进行验证"></Input></Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><Button type="primary" size = "small" @click = "bindEmail">保存</Button></Col><Col span="8"></Col><Col span="2" offset="12"></Col>
        </Row>
      </div>
      
      <div class = "info" v-if = "phoneOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="4"><label>当前手机:</label></Col><Col span="8">{{phone}}</Col><Col span="2" offset="10"><Button type="ghost" @click = "phoneOnEdit=false">放弃编辑</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="4"><label>新手机:</label></Col>
          <Col span="8"><Input v-model="phoneModel.phone" placeholder="请输入新手机" @on-blur="checkPhone"></Input></Col>&nbsp;&nbsp;
          <Col class= "error">{{errorMsg.phoneErrMsg}}</Col>
          <Col span="2" offset="10"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="4"><label>图形验证码:</label></Col>
          <Col span="8"><Input v-model="phoneModel.verifyCode" placeholder="请输入图形验证码"></Input></Col>&nbsp;&nbsp;
          <Col span = "2"><img :src = "phoneCaptcha" @click = "getVerifyCode" style = "height:3.4rem;border-radius:5px" title = "点击更换验证码" /></Col>
          <Col span="2" offset="8"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="4"><label>短信验证码:</label></Col>
          <Col span="8"><Input v-model="phoneModel.captcha" placeholder="请输入短信验证码"></Input></Col>&nbsp;&nbsp;
          <Col span = "2"><Button type="primary" @click = "getCaptcha" :disabled = "getCaptchaEnable">{{captchaTimeout == '60' ? '获取短信验证码':captchaTimeout + '秒后重新获取'}}</Button></Col>
          <Col span="2" offset="8"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><Button type="primary" size = "small" @click = "editPhoneOK">保存</Button></Col><Col span="8"></Col><Col span="2" offset="12"></Col>
        </Row>
      </div>
      
    </div>
    <div class ="module">
      <div class = "title" >密码</div>
      <div class = "info" v-if = "!pwdOnEdit">
        <Row type="flex" justify="start" class="code-row-bg">
          <Col span="2"><label>密码:</label></Col><Col span="8">*********</Col><Col span="2" offset="12"><Button type="ghost" @click = "pwdOnEdit=true">修改密码</Button></Col>
        </Row>
      </div>
      <div class = "info" v-if = "pwdOnEdit">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>旧密码:</label></Col><Col span="8"><Input v-model="pwdModel.oldpwd" type = "password" placeholder="请输入旧密码确认"></Input></Col><Col span="2" offset="12"><Button type="ghost" @click = "pwdOnEdit=false">放弃编辑</Button></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>新密码:</label></Col><Col span="8"><Input v-model="pwdModel.newpwd" type = "password" placeholder="请输入新密码"></Input></Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>确认密码:</label></Col><Col span="8"><Input v-model="pwdModel.repwd" type = "password" placeholder="再次输入新密码"></Input></Col><Col span="2" offset="12"></Col>
        </Row>
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><Button type="primary" size = "small" @click = "updatePwd">保存</Button></Col><Col span="8"></Col><Col span="2" offset="12"></Col>
        </Row>
      </div>
    </div>
    <div class ="module">
      <div class = "title" >第三方账号绑定</div>
      <div class = "info">
        <Row type="flex" justify="start" align="middle" class="code-row-bg">
          <Col span="2"><label>微信号:</label></Col><Col span="8">未绑定</Col><Col span="2" offset="12"><Button type="ghost" @click = "weixinOnEdit=true">绑定微信</Button></Col>
        </Row>
      </div>
    </div>
    <div id = "ModalContainer">
      <Modal
        v-model="modalVisible1"
        title="解绑邮箱"
        @on-ok="modalVisible2 = true"
        ok-text = "下一步"
        >
        <h3>确定要解绑“{{email}}吗？”</h3>	
        <p>解绑邮箱后，您将无法再使用该邮箱找回密码及登录操作</p>
      </Modal>
      

      <Modal v-model="modalVisible2">
        <p slot="header">
            <Icon type="information-circled"></Icon>
            <span>安全验证</span>
        </p>
        <div style="text-align:center">
          <p>为了保证您的账号安全，解绑邮箱前请进行安全验证</p>
          <Row type="flex" justify="center" align="middle" class="code-row-bg" style = "margin:2rem 0">
            <Col span="6" ><label>输入系统登录密码：</label></Col>
            <Col span="18" ><Input v-model="emailModel.pwdtemp" type = "password" placeholder="请输入登录密码"></Input></Col>
          </Row>
        </div>
        <div slot="footer">
            <Button type="primary" size="large" long  @click="unbindEmail">完成验证</Button>
        </div>
      </Modal>

    </div>
  </div>
</template>

<script>
export default {
  name: 'classes-setting',
  data () {
    return {
      modalVisible1:false,
      modalVisible2:false,
      phoneCaptcha:'',
      errorMsg:{
        emailErrMsg:'',
        phoneErrMsg:''
      },
      captchaTimeout:60,
      getCaptchaDisabled:false,
      basicInfoModel:{
        name:'',
        school:'',
        sno:''
      },
      emailModel:{
        pwdtemp:'',
        email:'',
        pwd:''
      },
      phoneModel:{
        phone:'',
        verifyCode:'',
        captcha:'',
      },
      pwdModel:{
        newpwd:'',
        repwd:'',
        oldpwd:''
      },
      basicInfoOnEdit:false,
      emailOnEdit:false,
      phoneOnEdit:false,
      pwdOnEdit:false,
      weixinOnEdit:false,
      basicInfo:{
        name:'',
        sno:'',
        school:'',
      }
    }
  },
  created(){
  },
  methods:{
    updateUserInfo(){
      var that = this;
      this.$axios.get('updateUserInfo.aurora',{
        params:that.basicInfoModel
      })
      .then(function(res){
        if(res.data.success == true){
          that.$Message.success(res.data.msg);
          that.basicInfoOnEdit = false;
          that.$store.commit('setUser',res.data.obj);
        }else{
        }
      })
      .catch(function(e){
        console.log(e);
      })

    },
    unbindEmail(){
      var that = this;
      this.$axios.get('unbindEmail.aurora',{
        params:{
          pwd:that.emailModel.pwdtemp
        }
      })
      .then(function(res){
        if(res.data.success == true){
          that.$Message.success(res.data.msg);
          that.emailOnEdit = false;
          that.modalVisible2 = false;
          that.$store.commit('setUser',res.data.obj);
          
        }else{
          that.$Message.success(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    bindEmail(){
      var that = this;
      if(this.checkEmail()){
          this.$axios.get('bindEmail.aurora',{
            params:that.emailModel
          })
          .then(function(res){
            if(res.data.success == true){
              that.$Message.success(res.data.msg);
              that.emailOnEdit = false;
              that.$store.commit('setUser',res.data.obj);
            }else{
              that.$Message.success(res.data.msg);
            }
          })
          .catch(function(e){
            console.log(e);
          })
      }else{
        that.$Message.warning('请确保邮箱格式正确');
      }
    },
    updatePwd(){
      var that = this;
      if(this.pwdModel.newpwd == this.pwdModel.repwd){
        this.$axios.get('updatePwd.aurora',{
        params:that.pwdModel
      })
      .then(function(res){
        if(res.data.success == true){
        
          that.$Message.success(res.data.msg);
          that.pwdOnEdit=false;
          
        }else{
          that.$Message.success(res.data.msg);
        }
      })
      .catch(function(e){
        console.log(e);
      })
      }else{
        this.$Message.warning('请确认两次输入的密码一致');
      }
    },
    editPhone(){
      this.phoneOnEdit = true;
      this.getVerifyCode(); 
    },
    editPhoneOK(){
      var that = this;
      if(this.checkPhone()){
        this.$axios.get('updatePhone.aurora',{
            params:that.phoneModel
          })
          .then(function(res){
            if(res.data.success == true){
              that.$Message.success(res.data.msg);
              that.phoneOnEdit = false;
              that.user.phone = that.phoneModel.phone;
            }else{
              that.$Message.warning(res.data.msg);
            }
          })
          .catch(function(e){
            console.log(e);
          })

      }else{
        that.$Message.warning('请确保邮箱格式正确');
      }

    },
    getVerifyCode(){
      var that = this;
      this.$axios.get('getVerifyCode.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.phoneCaptcha = res.data.obj;
        }else{
         
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
    getCaptcha(){
      this.getCaptchaEnable = true;
      var intevalId = setInterval(()=>{
        this.captchaTimeout--;
        if(this.captchaTimeout == 0){
          this.getCaptchaEnable = false;
          this.captchaTimeout = 60;
          clearInterval(intevalId);
        }
      },1000)
    },
    //输入验证
    checkPhone(){
      var rePhone = /^1\d{10}$/ ;
      if(!rePhone.test(this.phoneModel.phone)){
        this.errorMsg.phoneErrMsg = "电话号码错误";
        return false;
      }else{
        this.errorMsg.phoneErrMsg = '';
        return true;}
    },
    checkEmail(){
      var reEmail = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/ ;
      if(!reEmail.test(this.emailModel.email)){
        this.errorMsg.emailErrMsg = "邮箱格式错误";
        return false;
      }else{
        this.errorMsg.emailErrMsg = '';
        return true;}
    },
    errMsgClear(){
      for(var key in this.errorMsg){
        this.errorMsg[key] = ''
      }
    }
  
  },
  computed:{
    user(){
      return this.$store.getters.getUser;
      },
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    school(){
      if(this.user == null){
        return '';
      }else{
        return this.user.school;
      }
    },
    sno(){
      if(this.user == null){
        return '';
      }else{
        return this.user.sno;
      }

    },
    account(){
      if(this.user == null){
        return '';
      }else{
        return this.user.account;
      }

    },
    email(){
      if(this.user == null){
        return '';
      }else{
        return this.user.email;
      }

    },
    phone(){
      if(this.user == null){
        return '';
      }else{
        return this.user.phone;
      }
    },
  },
  watch:{
    basicInfoOnEdit(){
      if(this.user!=={} && this.basicInfoOnEdit==true){
        for(var key in this.basicInfoModel){
          this.basicInfoModel[key] = this.user[key];
        }
      }
    },
    emailOnEdit(){
      for(var key in this.emailModel){
        this.emailModel[key] = '';
      }
      this.errMsgClear();
    },
    pwdOnEdit(){
      for(var key in this.pwdModel){
        this.pwdModel[key] = '';
      }
      this.errMsgClear();
    },
    phoneOnEdit(){
      for(var key in this.phoneModel){
        this.phoneModel[key] = '';
      }
      this.errMsgClear();
    }
  
  }
}
</script>

<style scoped lang = "less">
@bor-c:#c8c8c8;
#userSetting {
  text-align:left;
  .module {
    + .module {
      margin-top:2rem;
    }
    > div {
      margin-top:1rem;
    }
    .title {
      border-bottom:1px solid #aaa;
      font-weight:bold;
    }
    .info {
      > div+div {
        margin-top:1rem;
      
      }
    }
  }
  .error {
    color:red;
  }
  
}
</style>



