<template>
  <div class = "course">
    <v-header></v-header>
    <div class = "main">
      <div class = "main-container">
        <transition name="slide-fade">
        <router-view></router-view>
        </transition>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>
<script>
import header from '@/components/topiccomment/header'
import details from '@/components/topiccomment/details'
import stats from '@/components/topiccomment/stats'
import footer from '@/components/common/footer'

export default({
  components:{
    'v-header':header,
    'v-footer':footer
  },
  stats:stats,
  details:details
})
</script>
<style lang = "less" scoped>
  @bor-c:#c8c8c8;
  .course {
    background-color: #f5eeec;
    .main {
      margin-top:2rem;
      height:100vh;
      overflow-x:hidden;
      width:100vw;
      padding-right: 20px;
    }
    .main-container {
      min-height:30rem;
      width:60%;
      margin:0 auto;
      border:1px solid #DCDCDC;
    }
  }

/*过渡效果*/
.slide-fade-enter-active {
  transition: all 2s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  /*transform: translateX(10px);*/
  opacity: 0;
}
</style>


