<template>
    <div class="homeworkview">
        <div type = "flex" class = "homeworkview-header">
          <div>
              <Button type = "text" style = "color:white" @click = "getHomeworks(hnId,null)">显示所有</Button>
              <Button type = "text" style = "color:white" @click = "getHomeworks(hnId,1)">只显示未批阅过的</Button>
          </div>
          <div style = "border-left:1px solid white;padding-left:1rem" @click = "$router.go(-1)">
            <img width= "24px" height = "24px" src = "../assets/images/icon/close-32.png"/>
          </div>
        </div>
        <Row type="flex">
            <Col span="4" class="homeworkview-menu-left">
                <Menu theme="dark" width="auto" @on-select = "onSelect" @on-open-change = "onOpenChange">
                    <div class="homeworkview-logo-left">
                        <div>当前显示:&nbsp;所有提交</div>
                        <div><Input placeholder="按姓名搜索"></Input></div>
                    </div>
                    <Submenu :name="h.id" v-for="h in hs" :key="h.id">
                        <template slot="title">
                            {{h.username}}
                        </template>
                        <MenuItem :name="file.id" v-for = "file in h.files" :key="file.id">{{file.name}}</MenuItem>
                    </Submenu>
                </Menu>
            </Col>
            <Col span="16">
                <div>
                  <pafviewer ref = "pafviewer"></pafviewer>
                </div>
            </Col>
            <Col span="4" class="homeworkview-menu-right">
              <div class = "homework-info">
                  <div>状态：{{hState}}</div>
                  <div>分数：<InputNumber :max="999" :min="0" v-model="hModel.score" placeholder="分数"></InputNumber></div>
                  <div>评语：</div>
                  <div><Input type="textarea" :autosize="{minRows: 3,maxRows: 10}" v-model="hModel.comment" placeholder="此处输入评语"></Input></div>
                  <div v-show="hasCurrent"><Button type = "primary" @click = "submitReview">{{hStateButtonText}}</Button><Button type = "primary" @click = "rejectH">打回</Button></div>
              </div>
              <div class = "homework-msg">
                  <div>作业留言:</div>
                  <p style = "margin-top:1rem;">{{hMsg}}</p>
              </div>
            </Col>
        </Row>
    </div>
</template>
<script>
import pafviewer from '@/components/common/pdfviewer';
export default {
  name:'homeworkview',
  data(){
    return {
      value:'',
      hs:[],
      currentH:null,
      hModel:{
          id:'',
          score:0,
          comment:''
      }
    }
  },
  created () {
      this.getHomeworks(this.hnId,1);
  },
  methods: {
    getHomeworks(id,state){
        this.currentH = null;
        this.cleanModel();
         var that = this;
        this.$axios.get('tea/getHomeworks.aurora',{
            params:{
                id:id,
                state:state
            }
        })
        .then(function(res){
            if(res.data && res.data.success){
                that.hs = res.data.obj;
                console.log(that.hs.length);
            }else{
                alert('出错了！情重试。');
            }
        })
        .catch(function(e){
            console.log(e);
        });
    },
    onSelect(fileId){
        this.$refs.pafviewer.start(fileId);
    },
    onOpenChange(homeworkId){
        this.hFilter(homeworkId);
    },
    submitReview(){
        var that = this;
        var searchparams = new URLSearchParams();
        for(var key in this.hModel){
            searchparams.append(key,this.hModel[key]);
        }
        this.$axios.post('tea/saveReview.aurora',searchparams)
        .then(function(res){
            if(res.data && res.data.success){
                that.$Message.success(res.data.msg);
                setTimeout(()=>{
                    that.arrayRemoveById(that.hs,that.hModel.id);
                    that.currentH = null;
                    that.cleanModel();
                },1000); 
            }
        })
        .catch(function(e){
            console.log(e);
        });
    },
    rejectH(){
        var that = this;
        this.$axios.get('tea/rejectHomework.aurora',{
            params:{
                id:that.hModel.id
            }
        })
        .then(function(res){
            if(res.data && res.data.success){
                that.$Message.success(res.data.msg);
                setTimeout(()=>{
                    that.arrayRemoveById(that.hs,that.hModel.id);
                    that.currentH = null;
                    that.cleanModel();
                },1000); 

            }else{
                alert('出错了！情重试。');
                
            }
        })
        .catch(function(e){
            console.log(e);
        });
        
    },
    //选中指定id值的作业，赋值给currentH
    hFilter(id){
        for(var key in this.hs){
            if(this.hs[key].id == id){
                this.currentH = this.hs[key];
                break;
            }
        }
    },
    cleanModel(){
        this.hModel.id = '';
        this.hModel.score = 0;
        this.hModel.comment = '';
    },
    arrayRemoveById(arr,id){
      for(var i = 0; i < arr.length; i++){
            if(arr[i].id == id)
              arr.splice(i,1);
      }
    }
  },
  computed: {
    hnId(){
        return this.$route.params.noticeId;
    },
    hMsg(){
        if(this.currentH == null){
            return '';
        }else{
            return this.currentH.msg;
        }
    },
    hState(){
        if(this.currentH == null){
            return '';
        }else{
            if(this.currentH.state == 2){
                return '已批阅过';
            }else{
                return '未批阅';
            }
        }
    },
    hStateButtonText(){
        if(this.currentH == null){
            return '';
        }else{
            if(this.currentH.state == 2){
                return '更新保存';
            }else{
                return '保存';
            }
        }
    },
    hasCurrent(){
        return this.currentH != null;
    }
  },
  watch: {
      currentH(){
        if(this.currentH != null){
            for(var key in this.hModel){
                this.hModel[key] = this.currentH[key];
            }
        }
      }
  },
  components: {
    pafviewer:pafviewer
  }
        
}
</script>
<style lang = "less" scoped>
    .homeworkview{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        width:100%;
    }
    .homeworkview-header{
        color:white;
        height:3rem;
        display: flex;
        justify-content:space-between;
        align-items: center;
        background: #282828;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
        padding:0 1rem;
    }
    .homeworkview-menu-left, .homeworkview-menu-right {
        background-color: #464c5b;
        color:white;
    }
    .homeworkview-logo-left{
        padding:.5rem;
        color:white;
        width: 90%;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
        >div+div {
            margin-top:.5rem;
        }
    }
    .homework-info {
      min-height:10rem;
      padding:1rem;
      border-bottom:1px solid white;
      > div {
        display: flex;
        align-items: center;
        justify-content:space-between;
        + div {
            margin-top:.5rem;
        }
      }
    }
    .homework-msg {
        padding:1rem;
    }
</style>


