<template>
  <div id = "sharefileviewer">
    <header>
      <div>{{name}}</div>
      <div>
        <Button type="text" style = "color:white" @click = "fullScreen">
          {{fullScreenText}}
        </Button>
      </div>
      <div @click = "back">
          <img src = "../../assets/images/icon/close-32.png"/>
      </div>
    </header>
    <div class = "main">
      <pafviewer ref = "pafviewer"></pafviewer>
    </div>
  </div>
</template>
<script>
import pafviewer from '@/components/common/pdfviewer';
export default({
  name:'sharefileviewer',
  data(){
    return {
      isFullScreen:false,
      fullScreenText:'全屏显示'

    }
  },
  mounted(){
    if(this.fileid != ''){
      this.$refs.pafviewer.start(this.fileid);
    }
    
  },
  methods:{
    fullScreen(){
      if(this.isFullScreen != true){
        var docElm = document.documentElement;
				//W3C  
				if (docElm.requestFullscreen) {  
            docElm.requestFullscreen(); 
            this.isFullScreen = true;
				}
				//FireFox  
				else if (docElm.mozRequestFullScreen) {  
            docElm.mozRequestFullScreen();
            this.isFullScreen = true;
				}
				//Chrome等  
				else if (docElm.webkitRequestFullScreen) {  
            docElm.webkitRequestFullScreen(); 
            this.isFullScreen = true;
				}
				//IE11
				else if (docElm.msRequestFullscreen) {
          docElm.msRequestFullscreen();
          this.isFullScreen = true;
				}
      }else{
        if (document.exitFullscreen) {  
            document.exitFullscreen();
            this.isFullScreen = false; 
        }  
        else if (document.mozCancelFullScreen) {  
            document.mozCancelFullScreen(); 
            this.isFullScreen = false;  
        }  
        else if (document.webkitCancelFullScreen) {  
            document.webkitCancelFullScreen(); 
            this.isFullScreen = false;  
        }
        else if (document.msExitFullscreen) {
              document.msExitFullscreen();
            this.isFullScreen = false; 
        }
      }
    },
    back(){
      this.$router.go(-1);
    }
  },
  computed:{
    file(){
      return this.$store.getters.getCurrentObject;
    },
    name(){
      if(this.file == null){
        return '';
      }else{
        return this.file.name;
      }
    },
    fileid(){
      if(this.file == null){
        return '';
      }else{
        return this.file.id;
      }
    }
  },
  watch:{
    isFullScreen(){
      if(this.isFullScreen == true){
        this.fullScreenText = '退出全屏';
      }else{
        this.fullScreenText = '全屏显示';
      }
    }
  },
  components:{
    pafviewer:pafviewer
  }

})
</script>
<style lang = "less" scoped>
  #sharefileviewer{
    width:100vw;
    overflow-x: hidden;
  }
  header {
	width:100%;
  height:3rem;
	background-color:#282828;
	color:white;
	padding:0 1rem;
	font-size:1.05rem;
	display:flex;
	align-items:center;
	justify-content:space-between;
}
header div:last-child {
	height:calc(100% - .2rem);
	padding:0 1rem;
	border-left:1px solid #969696;
	display:flex;
	align-items:center;
	justify-content:center;
	color:#969696;
}
header div+div:hover {
	color:#ddd;
	
}

.main {
	height:calc(100% - 3.3rem);
}


</style>


