<template>
  <div id = "drawer" :class = "{'isDrawerOut':isDrawerOut}" once>
<!--左边弹出抽屉-->
    <div id = "drawer-container" v-bind:class = "{'isDrawerOut':isDrawerOut}" ref = "drawer">
        <div id = "drawerMenu" @click = "isDrawerOut = false">
          <router-link to = "/classes"><div>课堂</div></router-link>
          <div v-if = "userType == 1">备课区</div>
          <div>私信</div>
        </div>
        <div id = "drawerItems">
          <a href = "javascript:void(0)" v-for = "item in classes" :key="item.id" @click = "hrefTo(item)">
            <span>{{item.name}}</span>
          </a>
        </div>
    </div>
    <div id = "drawer-handle" @click = "isDrawerOut = !isDrawerOut">
      <div style = "width:2rem;heith:3rem;transition:transform .5s;" :class = "{'isDrawerOutIcon':isDrawerOut}">
        <img src = "../../assets/images/icon/drawer-handle-32.png"/>
      </div>
    </div>
  </div>
</template>
<script>
  export default({
    data(){
      return {
        isDrawerOut:false,
      }
    },
    created(){
      
    },
    methods:{
      userType(){
      return this.$store.getters.getType;
      },
      getClass(){
        var that = this;
        this.$axios.get('getClass.aurora')
        .then(function(res){
          that.loadingClass = false;
          if(res.data.success == true){
            that.$store.commit('setClasses',res.data.obj);
          }else{
            
          }
        })
        .catch(function(e){
          console.log(e);
        });
      },
      hrefTo(item){
      this.$store.commit('setCurrentClass',item);
      this.$router.push('/classroom/'+ this.currentNav);
      this.isDrawerOut = false;
    },
    },
    computed: {
      classes(){
        return this.$store.getters.getClasses;
      },
      currentNav(){
        return this.$store.getters.getCurrentNav;
      }
    }
  });
</script>

<style lang = "less">
@bor-c:#c8c8c8;
/*左边弹出抽屉*/
#drawer {
  z-index: 999;
  position: fixed;
  top:0;
  bottom:0;
  left:-20rem;
  width:22rem;
  display: flex;
  transition: left .5s;
  #drawer-handle {
    display: flex;
    align-items: center;

  }
 
}
#drawer-container {
  width: 20rem;
  background-color:white;
  text-align:center;

  #drawerMenu {
  border-bottom: 1px solid @bor-c;
  div {
    padding:1.5rem 0;
    &:hover {
      background-color: #F7F7F7;
      border-bottom:1px solid green;
      }
    }
  }
  #drawerItems {
    padding-top:3rem;
    font-size:1rem;
    overflow-y:auto;
    overflow-x: hidden;
    >* {
      display: block;
      padding:1rem;
      &:hover {
        background-color:#F7F7F7;
        border-bottom:1px solid blue;
      }
    }
    
  }
}

/*左边抽屉过渡效果*/
.isDrawerOut {
  left:0!important;
  z-index: 1100!important;
}
.isDrawerOutIcon {
  background-color:rgba(255,241,241,0.8);
  transform:rotate(-180deg);
  z-index: 1100!important;
}

</style>


