<template>
  <div id = "submitHomework">
    <header>
      <div>
        <router-link to = "/classroom/homework">
        <Button type = "warning">{{submitstate ? '完成':'放弃提交'}}</Button>
        </router-link>
      </div>
      <div class = "nav">
        <Menu mode="horizontal" active-name="1">
          <MenuItem name="1">
              作业提交
          </MenuItem>
          <MenuItem name="2">
              作业讨论
          </MenuItem>
        </Menu>
      </div>
      <div></div>
    </header>
    <div class = "main">
      <div>
        <div class = "homework-name">{{name}}</div>
        <div></div>
      </div>
      <div>
        <div class = "info">
          <span>截止日期: {{$moment(new Date(homeworknotice.cutoffdatetime)).format('LLLL')}}</span>
          <span>需要查重</span>
        </div>
        <div>
          <span><Button type = "primary">查看谁交了</Button></span>
        </div>
      </div>
      <div>
        <div>
          <span><Button type = "primary" @click = "submitHomework">{{state == 0 ? '提交':'更新提交'}}</Button></span>
        </div>
        <div>
          <span>{{state == 0 ? '未完成':'更新提交'}}</span>
        </div>
      </div>
      <div style = "display:block;padding:0;">
        <div style = "min-height:10rem;padding:1rem">
          <p>文件列表：</p>
          <Upload
              ref = "upload"
              :default-file-list = "files"
              multiple
              type="drag"
              with-credentials
              :on-success = "uploadsuccess"
              :on-remove = "removefile"
              action="/eclass/stu/uploadHomeworkFile">
              <div style="padding: 20px 0">
                  <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                  <p>即将提交的文件<strong>（提交作业后，请不要随便移除已经提交的文件！)</strong></p>
                  <p style = "color:#aaa">（提交文档、图片，老师可在线批改,压缩包仅能下载）</p>
              </div>
          </Upload>
        </div>
        <div class = "message">
          <div>作业留言:</div>
          <div>
            <Input v-model="homeworkModel.msg" type="textarea" :autosize="true" placeholder="添加留言，作业要以附件的形式提交，而不是写在这里......"></Input>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default({
    data(){
      return {
        homework:null,
        submitstate:false,
        files:[],
        homeworkModel:{
          id:'',
          msg:'',
          files:[]
        }
      }
    },
    created(){
      this.getHomework();
    }, 
    computed:{
      homeworknotice(){
        return this.$store.getters.getCurrentObject;
      },
      name(){
        if(this.homework==null){
          return "";
        }else{
          return this.homework.name;
        }
      },
      state(){
        if(this.homework==null){
          return 0;
        }else{
          return this.homework.state;
        }
      }
    },
    methods:{
      uploadsuccess(res, file, fileList){
        this.homeworkModel.files.push(res.obj);
        
      },
      removefile(file, fileList){
        var that = this;
        this.$axios.get('stu/removeHomeworkfile.aurora',{
          params:{
            hid:that.homework.id,
            fid:file.id,
          }
        })
        .then(function(res){
          if(res.data && res.data.success){
            that.$Message.success(res.data.msg);
          }
        })
        .catch(function(e){
          console.log(e);
        });

      },
      getHomework(){
        var that = this;
        this.$axios.get('stu/getHomework.aurora',{
          params:{
            hnId:that.homeworknotice.id
          }
        })
        .then(function(res){
          if(res.data && res.data.success){
            that.homework = res.data.obj;
            that.homeworkModel.id = res.data.obj.id;
            that.homeworkModel.msg = res.data.obj.msg;
            that.files = res.data.obj.files;
          }
        })
        .catch(function(e){
          console.log(e);
        })
      },
      submitHomework(){
        var that = this;
        var params =  new URLSearchParams(); 
        params.append('id',this.homework.id);
        params.append('msg',this.homeworkModel.msg);
        params.append('files',this.homeworkModel.files);
        this.$axios.post('stu/submitHomework.aurora',params)
        .then(function(res){
          if(res.data && res.data.success){
            that.submitstate = true;
            that.$Message.success(res.data.msg);
          }
        })
        .catch(function(e){
          console.log(e);
        });
        
      }
    }
  })
</script>
<style scoped lang = "less">
@bor-c:#c8c8c8;
  #submitHomework {
    margin-top:2rem;
    padding-top:1rem;
    position:relative;
    border:1px solid @bor-c;
    background-color:white;
  }
  header {
    padding:1rem;
    text-align: center;
    border-bottom:1px solid #aaa;
    display:flex;
    justify-content:space-between;
  }
  .main {
    > div {
      padding:0 1rem;
      display: flex;
      justify-content: space-between;
      margin-top:1rem;
      .homework-name {
        font-weight:bold;
        font-size:1.2rem;
      }
      .info {
        span {
          padding:.5rem 2rem;
          background-color:rgba(0,0,0,.1);
          + span {
            margin-left:1rem;
          }
        }

      }
      .message {
        background-color:#f6f6f6;
        padding:2rem;
        display:flex;
        > div:first-child {
          flex:1;
        }
        > div + div {
          flex:9;
        }
      }

    }
  }
</style>


