<template>
  <div class = "member">
    <div class = "options">
      <div>
        <span><Button type = "primary">导入成员</Button></span>
        <span style = "margin-left:2rem;">下载成员信息</span>
      </div>
      <div>
        <span>
          <Input placeholder="学号，姓名" size = "small">
            <Button slot="append" icon="ios-search" size = "small"></Button>
          </Input>
        </span>
      </div>
    </div>
    <div class = "notice" v-show="freeJoinGroup">
      已开启『允许选组』 , 务必在选组结束后关闭此功能，以免学生随意更换小组。
      <Button type = "text" style = "position:absolute;right:.5rem;top:1rem"><Icon type="close"></Icon></Button>
    </div>
    <div class = "main">
      <Row type="flex">
        <Col span="6">
          <div class = "main-container main-left">
            <div class = "top">
              <div style = "color:black">
                <div style = "padding:.5rem 1rem">学生加课码</div>
                <div style = "padding:.5rem 1rem;display:flex;justify-content:space-between;align-items:center">
                  <div style = "font-weight:bold;font-size:1.2rem">JJSSDD</div>
                  <div>
                    <Dropdown trigger="click" style="margin-left: 20px" v-if="userType == 1">
                      <a href="javascript:void(0)">
                        <span><Icon type="ios-keypad"></Icon></span>
                      </a>
                      <DropdownMenu slot="list">
                          <DropdownItem><Button type = "text" >停用</Button></DropdownItem>
                          <DropdownItem><Button type = "text" >重置</Button></DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </div>
                </div>
              </div>
              <div style = "display:flex;align-items:center;height:4rem" v-if= "userType == 1">
                <div style = "flex:1" class = "add-group">
                  <a href = "javascript:void(0)" style = "border:1px dashed #c8c8c8;padding:.6rem 1rem" @click = "createGroup">
                    添加分组
                  </a>
                </div>
                <div style = "flex:1">
                  <Tooltip content="开启此功能，学生可自主选组。" placement="right">
                    <Checkbox v-model="freeJoinGroup" @on-change = "changeGroupSetting">允许选组</Checkbox>
                  </Tooltip>
                </div>
              </div>
            </div>
            <div class = "groups">
              <div @click = "currentGroup = teachers" :class = "{'groupSelected': currentGroupName == '教学团队'}">教学团队</div>
              <div @click = "currentGroup = students" :class = "{'groupSelected': currentGroupName == '全部学生'}">全部学生</div>
              <div class = "group" v-for = "(item,index) in groups" :key="item.id" :class = "{'groupSelected': groupSelectedFilter(item.id)}" @click = "currentGroup = item">
                <div>{{item.name}}</div>
                <div class = "hovershow">
                  <Dropdown trigger="click" style="margin-left: 20px" v-if = "userType == 1">
                      <a href="javascript:void(0)">
                        <span><Icon type="ios-keypad"></Icon></span>
                      </a>
                      <DropdownMenu slot="list">
                          <DropdownItem><Button type = "text" >重命名</Button></DropdownItem>
                          <DropdownItem><Button type = "text" >删除</Button></DropdownItem>
                      </DropdownMenu>
                  </Dropdown>
                </div>
              </div>
            </div>
          </div>
        </Col>
        <Col span="18">
          <div class = "main-container main-right">
            <div class = "group-options">
              <div style = "font-size:1.2rem">{{currentGroupName}} {{stus.length == 0 ? '':'('+stus.length+')'}}</div>
              <div>
                <div>
                  <Button type = "primary" @click = "joinGroup" v-if = "userType == 0" v-show="currentGroup != null">加入该组</Button>
                </div>
              </div>
            </div>
            <div class = "group-stus">
              <div v-for="item in stus" :key="item.id">
                <div>
                  <Avatar :src="item.icon" size = "large" :title = "item.name"/>
                </div>
                <div>{{item.sno}}</div>
                <div>{{item.name}}</div>
                <div>{{item.email}}</div>
                <div style = "min-width:5rem">
                  <div class = "hovershow">
                    <Dropdown trigger="click" style="margin-left: 20px">
                      <a href="javascript:void(0)">
                        <span><Icon type="ios-keypad"></Icon></span>
                      </a>
                      <DropdownMenu slot="list">
                          <DropdownItem><Button type = "text" >编辑名片</Button></DropdownItem>
                          <DropdownItem><Button type = "text" @click = "sendMsg(item)">私信</Button></DropdownItem>
                          <DropdownItem><Button type = "text" >删除此成员</Button></DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
    <div class = "modals">
      <Modal
        v-model="modal1"
        title="新建分组"
        @on-ok="createGroupOK"
        >
        <div>
          <Form>
            <FormItem label="分组名称">
              <Input v-model = "groupModel.name" placeholder="输入分组名"></Input>
            </FormItem>
          </Form>
        </div>
      </Modal>

      <Modal
        v-model="modal2"
        :width = "70"
        :scrollable = "false"
        :transfer = "true"
        :title = "chattingTarg"
        @on-cancel = "$refs.chattingView.stopChatting()"
        >
        <div>
          <component :is = "chattingView" ref = "chattingView"></component>
        </div>
        <div slot="footer"></div>
      </Modal>
    </div>
  
  </div>
</template>
<script>
import chatting from '@/components/common/chatting'
export default({
  name:'member',
  data(){
    return{
      modal1:false,
      modal2:false,
      freeJoinGroup:false,
      groupModel:{
        name:''
      },
      groups:[],
      teachers:[],
      students:[],
      currentGroup:null,
      chattingView:chatting,
      chattingTarg:"",
    }
  },
  components: {
    chatting:chatting
  },
  created () {
    this.$store.commit('setCurrentNav','member');
    this.getGroups();
  },
  methods: {
    createGroup(){
      this.modal1 = true;
      this.groupModel.name = '';
    },
    createGroupOK(){
      var that = this;
      this.$axios.get('tea/createGroup.aurora',{
        params:{
          classId:that.classId,
          name:that.groupModel.name,
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
        }else {
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    getGroups(){
      var that = this;
      this.$axios.get('getGroup.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.teachers = res.data.obj.teachers;
          that.students = res.data.obj.students;
          that.groups = res.data.obj.groups;
          this.freeJoinGroup = (res.data.obj.setting.freejoingroup == 1);
        }else {
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    changeGroupSetting(){
      var that = this;
      this.$axios.get('tea/changeGroupSetting.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
        }else {
          that.freeJoinGroup = !that.freeJoinGroup;
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
      
    },
    //加入分组
    joinGroup(){
      var that = this;
      this.$axios.get('stu/joinGroup.aurora',{
        params:{
          id:that.currentGroup.id
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
        }else {
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    //为选中的item添加样式
    groupSelectedFilter(groupId){
      if(this.currentGroup != null){
        return this.currentGroup.id == groupId;
      }else {
        return false;
      }
    },
    //私信部分
    sendMsg(member){
      this.chattingTarg = member.name;
      this.modal2 = true;
      this.$refs.chattingView.startChatting(member);
    }
  },
  computed: {
    userType(){
      return this.$store.getters.getType;
    },
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    currentGroupName(){
      if(this.currentGroup != null){
        return this.currentGroup.name;
      }else {
        return '';
      }
    },
    stus(){
      if(this.currentGroup != null){
        return this.currentGroup.users;
      }else {
        return [];
      }
    }
  },
  watch: {
    
  }

})
</script>
<style lang = "less" scoped>
   @bor-c:#c8c8c8;
  .member {
    background: white;
    min-height: inherit;
    padding:2rem;
    .options {
      display: flex;
      justify-content: space-between;
      > div {
        display: flex;
        align-items: center;
      }
    }
    .notice {
      position: relative;
      background: #ffffd2;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top:1rem;
      height:4rem;
      color: #94463f;
    }
    .main {
      margin-top:2rem;
      border:1px solid @bor-c;
      text-align: center;
      .main-left {
        border-right:1px solid @bor-c;
        .top {
          padding:.5rem;
          text-align: left;
          > div {
            &:first-child {
              background: #dbe9ff;
            }
          }
        }
        .groups {
          text-align: left;
          > div {
            padding:1rem;
            display: flex;
            justify-content: space-between;
            .hovershow {
              display: none;
            }
            &:hover {
              background-color:#dbe9ff;
              .hovershow {
                display: block;
              }
            }
          }
        }
        
      }
      .main-right {
        .group-options {
          border-bottom:1px solid @bor-c;
          display: flex;
          justify-content: space-between;
          align-items:center;
          padding:1.5rem;
        }
        .group-stus {
          > div {
            border-bottom:1px solid @bor-c;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding:.5rem 1.5rem;
            .hovershow {
              display: none;
            }
            &:hover {
              background-color: #fee;
              .hovershow {
                display: block;
              }
            }
          }

        }
      }
      .main-container {
        min-height:40rem;
      }
      
    }
    
  }
  /*其它选项*/
  .add-group {
    a {
      &:hover {
        color:blue!important;
        border-color: blue!important;
      }
    }
  }
  /*分组被选中后的样式*/
  .groupSelected {
    background: #4d90fe!important;
    font-weight: bold;
  }
</style>


