<template>
  <div class = "register-pan">
    <div>
      <span><router-link to = "/register"><Icon type="chevron-left"></Icon></router-link></span>  <span>{{typeName + '注册'}}</span>
    </div>
	
    <div class = "reg-input">
      <Form ref="form" :model="form" :rules="rules" :label-width="100">
      
        
        <FormItem label="邮箱/手机" prop = "identity" :required = "true">
          <Input v-model="form.identity"  ></Input>
        </FormItem>

        <FormItem label="密码">
          <Input v-model="form.pwd"  type="password"></Input>
        </FormItem>
        <FormItem label="姓名">
          <Input v-model="form.name"  ></Input>
        </FormItem>
        
        <FormItem label="学号" v-if = "this.$route.params.type == 'stu'">
          <Input v-model="form.sno"  ></Input>
        </FormItem>
        
        <FormItem label="学校"  >
          <Input v-model="form.school"  ></Input>
        </FormItem>
        <FormItem label="验证码" >
          <Input v-model="form.verifycode" @on-enter = "reg"></Input>
        </FormItem>
      </Form>
    </div>
    <div class = "reg-captcha">
      <img :src = "phoneCaptcha" title = "点击更换验证码" @click = "getVerifyCode">
    </div>
    <div style = "color:red;">
      {{msg}}
    </div>
    <div class = "reg-button">
      <Button type="primary" @click = "reg">{{'注册为' + typeName}}</Button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    var rePhone = /^1\d{10}$/ ;
	  var reEmail = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/ ;
    
    var validateIdentity = (rule, value, callback) => {
      if (value === '') {
          callback(new Error('请输入电话或邮箱'));
        } else {
          if(reEmail.test(value)){
            this.form.email = value;
            callback();
          }else if(rePhone.test(value)){
            this.form.phone = value;
            callback();
          }else{
            callback(new Error('电话或邮箱错误'));
          }
        }
    };
    return {
      phone:'',
      phoneCaptcha:'',
      captcha:'',
      msg:'',
      form:{
        identity:'',
        type:0,
        name:'',
        son:'',
        pwd:'',
        school:'',
        email:'',
        phone:'',
        verifycode:'',
        msg:'',
      },
      rules:{
        identity: [
              { validator: validateIdentity, trigger: 'blur' }
        ]
      }
    }
  },
  computed:{
    typeName:function(){
	  if(this.$route.params.type == "stu")
	    return '学生'
	  else if(this.$route.params.type == "tea"){
      this.form.type = 1;
      return '教师/助教'
    }
	    
	}
  },
  created(){
    this.getVerifyCode();
  },
  methods:{
    reg:function(){
      var searchparams = new URLSearchParams();//Using application/x-www-form-urlencoded format  
      for(var prop in this.form){
        searchparams.append(prop,this.form[prop]);
      }
      if(true){
        var that = this;
        this.$axios.post('reg.aurora',searchparams)
          .then(function (response) {
              if(response.data.success){
                that.$router.push('/login')
              }else{
                that.getVerifyCode();
                that.msg = response.data.msg;
                that.form.verifycode = '';
              }
          })
          .catch(function (error) {
             console.log(error);
          });
      }
    },
    preReg:function(){
      var searchparams = new URLSearchParams();//Using application/x-www-form-urlencoded format  
      for(var prop in this.form){
        searchparams.append(prop,this.form[prop]);
      }
      var data;
      this.$axios.post('checkRegState.aurora',searchparams)
          .then(function (response) {
            //if(response.data.success == false)
              //throw new Error(response.data.msg);
            data = response.data;
          })
          .catch(function (error) {
              throw error;
          });
      if(data && data.success == true)
        throw new Error(data.msg);
    },
    getVerifyCode(){
      var that = this;
      this.$axios.get('getVerifyCode.aurora')
      .then(function(res){
        if(res.data.success == true){
          that.phoneCaptcha = res.data.obj;
        }else{
         
        }
      })
      .catch(function(e){
        console.log(e);
      })
    },
  }
}

</script>


<style scoped lang = "less">

@bor-c:#c8c8c8;

.register-pan {
  padding:1rem;
  border:1px solid @bor-c;
  min-width:28rem;
  background-color:rgba(255, 251, 240, .7);
  > div:first-child {
    display:flex;
	> * {
	  text-align:left;
	  padding-left:1rem;
	  &:first-child {
	   flex:1;
	  }
	  &:last-child {
	   flex:7;
	   border-left:1px solid @bor-c;
	  }
	
	}
  }
}
.reg-input {
  margin-top:2rem;
  padding-right:2rem;

}
.reg-captcha {
  text-align:right;
  padding-right:2rem;
  img {
    width:12rem;
	  height:4rem;
    border-radius:5px;
  }
}
.reg-button {
  display:flex;
  margin:.5rem 0;
  padding:0 1rem;
  > * {
    flex:1;
  }
}
.reg-msg {
  color:red;
  font-size:.8rem;
}
</style>



