<template>
  <header>
    <div>
      <div>
        <span><router-link to="/classes/main"><Button type="text"  >课堂</Button></router-link></span>
        <span><router-link to="/login"><Button type="text"  >私信</Button></router-link></span>
        <span><router-link to="/login"><Button type="text"  >文库</Button></router-link></span>
        <span><router-link to="/login"><Button type="text"  >课程资源</Button></router-link></span>
      </div>
    </div>
    <div>
      <span><router-link to="/login"><Icon type="ios-search-strong" size = "28"></Icon></router-link></span>
      <span><router-link to="/login"><Badge :value="200" :max="99" class="item"><Icon type="ios-bell-outline" size = "28"></Icon></Badge></router-link></span>
      <span>
        <Dropdown>
            <span>
              <Avatar :src = "icon" size = "large" :title = "name"/>
            </span>
            <DropdownMenu slot="list">
                <DropdownItem><router-link to="/classes/setting"><Button type="text" size="small"><Icon type="android-settings"></Icon>个人设置</Button></router-link></DropdownItem>
                <DropdownItem><Button type="text" size="small"><Icon type="android-download"></Icon>文库下载</Button></DropdownItem>
                <DropdownItem><Button type="text" size="small" @click="logout"><Icon type="log-out"></Icon>退出登录</Button></DropdownItem>
            </DropdownMenu>
        </Dropdown>
      </span>
    </div>
  </header>
</template>

<script>
export default {
  name: 'header',
  data () {
    return {
    }
  },
  methods:{
    logout(){
      var that = this;
      this.$axios.get('logout')
      .then(function(res){
        if(res.data.success == true){
          that.$router.push('/login');
        }
      })
      .catch(function(e){
        console.log(e);
      });
    }
  },
  computed:{
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    icon(){
      if(this.user == null){
        return '';
      }else{
        return this.user.icon;
      }
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang = "less">
  @bor-c:#c8c8c8;
  header {
    height:5rem;
    display:flex;
    text-align:center;
    background-color:rgba(255, 251, 240, 0.6);
    padding:0 2rem;
    > div {
      display:flex;
      align-items:center;
      span {
        margin:1em;
        * {
          color:#2d2d2d;
        }
      }
      &:first-child {
        * {
          font-size:1.2rem;
        }
        
        flex:5;
      }
      &:last-child {
        flex:2;
        flex-direction:left;
        justify-content:flex-end;
      }
      
    }
  }
</style>



