export default({
  state:{
    userId:'',
    user:null,
    type:-1,
    classes:[],
    currentClass:null,
    currentClassId:'',
    currentObject:null,//当前视图需要的零时存储的对象,全局当前视图单例
    currentNav:'homework'//default homework
  },
  mutations:{
    setUserId(state,userId){
      state.userId = userId;
    },
    setUser(state,user){
      state.user = user;
      window.sessionStorage.setItem('user',JSON.stringify(user));
    },
    setType(state,type){
      state.type = type;
      window.sessionStorage.setItem('type',type);
    },
    setClasses(state,classes){
      state.classes = classes;
      window.sessionStorage.setItem('classes',JSON.stringify(classes));
    },
    setCurrentClass(state,clazz){
      state.currentClass = clazz;
      window.sessionStorage.setItem('currentClass',JSON.stringify(clazz));
      state.currentClassId = clazz.id;
      window.sessionStorage.setItem('currentClassId',clazz.id);
    },
    setCurrentObject(state,obj){
      state.currentObject = obj;
      window.sessionStorage.setItem('currentObject',JSON.stringify(obj));
    },
    setCurrentNav(state,targNav){
      state.currentNav = targNav;
      window.sessionStorage.setItem('currentNav',targNav);
    },
    removeClassById(state,classId){
      for(var i = 0; i < state.classes.length; i++){
        if(state.classes[i].id == classId)
          state.classes.splice(i,1);
      }
    },
    addClass(state,clazz){
      state.classes.push(clazz);
    }
  },
  actions:{},
  getters:{
    getType: state => {
      if(state.type == -1){
        state.type = window.sessionStorage.getItem('type');
      }
      return state.type;
    },
    getCurrentClass: state => {
      if(state.currentClass == null){
        state.currentClass = JSON.parse(window.sessionStorage.getItem('currentClass'));
      }
      return state.currentClass;
    },
    getCurrentClassId: state => {
      if(state.currentClassId == ''){
        state.currentClassId = window.sessionStorage.getItem('currentClassId');
      }
      return state.currentClassId;
    },
    getClasses: state => {
      if(state.classes.length == 0){
        state.classes = JSON.parse(window.sessionStorage.getItem('classes'));
      }
      return state.classes;
    },
    getCurrentObject: state => {
      if(state.currentObject == null){
        state.currentObject = JSON.parse(window.sessionStorage.getItem('currentObject'));
      }
      return state.currentObject;
    },
    getCurrentNav:state => {
      if(state.currentNav == ''){
        state.currentNav = window.sessionStorage.getItem('currentNav');
      }
      return state.currentNav;
    },
    getUser: state => {
      if(state.user == null){
        state.user = JSON.parse(window.sessionStorage.getItem('user'));
      }
      return state.user;
    }

  },
  modules:{},
  pludins:[],

})


