<template>
  <div class = "topic-details">
    <div class = "main topic-main">
      <div style = "display:flex">
        <div style = "flex:8;padding-right:2rem;max-width:100%;overflow: hidden;">
          <h2 style = "font-weight:normal;margin-bottom:2rem">{{topicName}}</h2>
          <div style = "font-size:.8rem;margin-bottom:1rem"><span>楼主: {{topicAuthor}}</span>&nbsp;&nbsp;<span>发起时间：{{$moment(new Date(topicCreatedatetime)).format('LLL')}}</span></div>
        </div>
        <div style = "flex:4;display:flex;justify-content:center;align-items:center">
          <div><Button type = "primary" size = "small">展示词云</Button></div>
          <div style = "font-size:.8rem;margin-left:1rem;display:flex;align-items:center"><i-switch></i-switch>&nbsp;&nbsp;弹幕已关闭</div>
        </div>
      </div>
      <p v-html="topicContent" style = "max-width:100%;overflow:hidden"></p>
    </div>
    <div class = "main comment-main">
      <div class = "comment-edit">
        <div style = "flex:1">
            <Avatar :src="icon" size = "large" :title = "name"/>
        </div>
        <div style = "flex:99;display:flex;align-items:center;margin-left:1rem">
            <div v-show = "!commentOnEdit" @click = "commentOnEdit = true" style = "width:100%">
              点击添加评论...
            </div>
            <div v-show = "commentOnEdit" style = "flex:100">
              <div>
                <Input type = "textarea" :rows = "4" placeholder="添加评论" v-model="model.content"></Input>
              </div>
              <div style = "text-align:right;margin-top:2rem">
                <span>
                  <Button @click = "commentOnEdit = false">取消</Button> 
                </span>
                <span style = "margin-left:1rem">
                  <Button type = "primary" @click="commentTopic">确定</Button>
                </span>
              </div>
            </div>
        </div>
      </div>
      <div class = "comment-list">
        <div class = "comment-list-item" v-for="(item,index) in comments" :key="item.id">
          <div style = "flex:1">
            <Avatar :src="item.usericon" size = "large" :title = "item.username"/>
          </div>
          <div style = "flex:99;margin-left:1rem">
            <div class = "header">
              <div><span>{{item.username}}</span><span style = "margin-left:1.5rem;color:#aaa;font-size:.8rem">12月27日 18:05</span><span style = "margin-left:1rem;color:#aaa;font-size:.8rem">#{{comments.length - index}}楼</span></div>
              <div class = "hover-show">
                <Button style = "font-size:1.5rem" type = "text" @click = "reply(item.username)"><Icon type="reply"></Icon></Button>
                <Button type = "text" style = "font-size:1.5rem" v-if = "userType==1" @click="deleteComment(item.id)"><Icon type="trash-a"></Icon></Button>
              </div>
            </div>
            <div class = "main">
              {{item.content}}
            </div>
            <div class = "footer">
              <span><Icon type="thumbsup"></Icon></span>&nbsp;<span>1</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      commentOnEdit:false,
      topic:null,
      comments:[],
      model:{
        content:''
      }
    }
  },
  created () {
    this.$store.commit('setCurrentNav','comment-details');
    this.getTopicComments();
  },
  methods: {
    getTopicComments(){
      var that = this;
      this.$axios.get('getTopicComment.aurora',{
        params:{
          topicId:that.$route.params.id
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.topic = res.data.obj.topicnotice;
          that.comments = res.data.obj.comments;
        }else {
          alert('出错了，请重试或重新登录。');
        }

      })
      .catch((e)=>{
        console.log(e);

      })
    },
    commentTopic(){
      var that = this;
      var params = new URLSearchParams();
      params.append('content',this.model.content);
      params.append('topicId',this.$route.params.id);
      this.$axios.post('commentTopic.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.comments.unshift(res.data.obj);
          that.$Message.success(res.data.msg);
          that.commentOnEdit = false;
        }else {
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);

      });
    },
    reply(username){
      this.model.content = '@ ' + username + ' , ';
      this.commentOnEdit = true;
    },
    deleteComment(id){
      var that = this;
      function del(){
        that.$axios.get('deleteComment.aurora',{
          params:{
            id:id
          }
        })
        .then((res)=>{
          that.$Message.success(res.data.msg);
          that.arrayRemoveById(that.comments,id);
          that.$Modal.remove();
        })
        .catch((e)=>{
          console.log(e);
        });
      }
      this.$Modal.confirm({
                    title: '确认删除该评论？',
                    content: '',
                    loading: true,
                    onOk: () => {
                      del();
                    }
                });
    },
    arrayRemoveById(arr,id){
      for(var i = 0; i < arr.length; i++){
            if(arr[i].id == id)
              arr.splice(i,1);
      }
    }
  },
  computed: {
    user(){return this.$store.getters.getUser},
    name(){
      if(this.user == null){
        return '';
      }else{
        return this.user.name;
      }
    },
    icon(){
      if(this.user == null){
        return '';
      }else{
        return this.user.icon;
      }
    },
    userType(){
      return this.$store.getters.getType;
    },
    topicName(){
      if(this.topic == null){
        return '';
      }else {
        return this.topic.name;
      }
    },
    topicAuthor(){
      if(this.topic == null){
        return '';
      }else {
        return this.topic.author;
      }
    },
    topicCreatedatetime(){
      if(this.topic == null){
        return '';
      }else {
        return this.topic.createdatetime;
      }
    },
    topicContent(){
      if(this.topic == null){
        return '';
      }else {
        return this.topic.content;
      }
    }
  },
  watch:{
    commentOnEdit(){
      if(this.commentOnEdit==false){
        this.model.content = '';

      }
    }
  }
  
}
</script>
<style lang="less" scoped>
  @bor-c:#c8c8c8;
  .topic-details {
    background-color: white;
    min-height: inherit;
    .main {
     
    }
    .topic-main {
      
       padding:2rem;

    }
    .comment-main {
      border-top:1px solid @bor-c;
      background:#f6f6f6;
      min-height:20rem;
      .comment-edit {
        padding:1rem 2rem;
        display: flex;
      }
      .comment-list {
        border-top:1px solid @bor-c;
        .comment-list-item {
          &:nth-child(even){
            border-top:1px solid @bor-c;
          }
          display: flex;
          padding:1rem 2rem;

          .hover-show {
              display:none;
          }

          &:hover {
            .hover-show {
              display: block;
            }
          }
          .header {
            display: flex;
            justify-content:space-between;
            align-items:center;
            line-height: 4rem;
          }
          .main {
            
          }
          .footer {
            margin-top:.5rem;
            text-align: right;
            padding-right:3rem;
            font-size:1.2rem;
          }

        }


      }


    }











  }


</style>



