<template>
  <div class = "performance">
    <div class = "options">
      <div>
        <span><Button type = "primary" @click = "modal1=true">新建表现</Button></span>
        <span style = "margin-left:2rem;">下载报表</span>
      </div>
      <div>
        <span>
          <Input placeholder="学号，姓名" size = "small">
            <Button slot="append" icon="ios-search" size = "small"></Button>
          </Input>
        </span>
      </div>
    </div>
    <div class = "main">
      <Row type="flex">
        <Col span="6">
          <div class = "main-container main-left">
            <div style = "padding:0 1rem">
              <Dropdown>
                  <Button type = "text" style = "border:1px solid #c8c8c8">全部成员&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon></Button>
                  <DropdownMenu slot="list">
                      <DropdownItem><Button type = "text" size="small" disabled>分组1</Button></DropdownItem>
                      <DropdownItem><Button type = "text" size="small" >分组2</Button></DropdownItem>
                  </DropdownMenu>
              </Dropdown>
            </div>
            <div class = "student" v-for = "(item,index) in stus" :key="index">
              <div>{{item.sno}}</div>
              <div style = "font-weight:bold">{{item.username}}</div>
              <div class = "hovershow">
                <div>共&nbsp;0&nbsp;<Icon type="star"></Icon></div>
              </div>
            </div>
          </div>
        </Col>
        <Col span="18">
          <div class = "main-right">
            <div class = "main-container  main-right-container">
              <div>
                <div class = "performance-info" v-for = "(item,index) in performanceNotices" :key="item.id">
                  <div>{{item.name}}</div>
                  <div class = "hoverhidden">{{$moment(item.createdatetime).format("YYYY/MM/DD hh:mm")}}</div>
                  <div class = "hovershow">
                    <Button type = "text" title = "编辑" @click = "editPerformanceNotice(item)"><Icon type="edit"></Icon></Button>
                    <Button type = "text" title = "删除" @click = "deletePerformanceNotice(item)"><Icon type="trash-a"></Icon></Button>
                  </div>
                </div>
              </div>
              <div v-for = "(item,y) in stus" :key="y">
                <div @mouseover="setHover(x,y)" v-for = "(subitem,x) in item.performances" :key="subitem.id"><Button type = "text" @click = "changePerformanceScore(item.username,subitem)">{{subitem.score}}&nbsp;<Icon type="star"></Icon></Button></div>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </div>
    <div>
      <Modal
        v-model="modal1"
        title="新建考勤"
        @on-ok="createPerformance"
        >
        <div>
          <Form>
            <FormItem label="考勤名称">
              <Input v-model = "model.name" placeholder="输入考勤名称"></Input>
            </FormItem>
          </Form>
        </div>
      </Modal>
      
      <Modal
        v-model="modal4"
        title="重命名表现"
        ok-text = "确定"
        cancel-text = "取消"
        @on-ok="editPerformanceNoticeOK"
        >
        <div>
          <Form>
            <FormItem label="考勤名称">
              <Input v-model = "currentPerformanceNoticeModel.name" placeholder="输入考勤名称"></Input>
            </FormItem>
          </Form>
        </div>
      </Modal>


      <Modal
        v-model="modal3"
        :title="currentPerformanceUserName + '  的表现'"
        ok-text = "确定变更"
        cancel-text = "取消"
        @on-ok="changePerformanceScoreOK"
        >
        <div style  = "display:flex;justify-content:center;align-items:center">
          <Button type = "text">共&nbsp;{{starCount}}&nbsp;<Icon type="star"></Icon></Button>
          <Rate  show-text v-model="currentPerformanceScore" :count = "starCount"></Rate>
          <Button type = "text" @click = "starCount++" title = "点击添加"><Icon type="plus"></Icon></Button>
        </div>
      </Modal>

    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      modal1:false,
      modal3:false,
      modal4:false,
      hover:{
        x:0,
        y:0
      },
      performanceNotices:[],
      stus:[],
      currentPerformance:null,
      currentPerformanceNotice:null,
      currentPerformanceScore:0,
      starCount:5,
      currentPerformanceNoticeModel:{
        id:'',
        name:''
      },
      model:{
        name:''
      }
    }
  },
  created(){
    this.$store.commit('setCurrentNav','performance');
    this.getPerformances();
  },
  methods:{
    setHover(x,y){
      this.hover.x = x;
      this.hover.y = y;
    },
    //新建表现
    createPerformance(){
      var that = this;
      this.$axios.get('tea/createPerformanceNotice.aurora',{
        params:{
          name:that.model.name,
          classId:that.classId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.getPerformances();
        }else{
          alert('出错了，请重试或重新登录');
        }
      })
      .catch((e)=>{
        console.log(e);
      });

    },
    //获取班级所有表现状态
    getPerformances(){
      var that = this;
      this.$axios.get('tea/getPerformance.aurora',{
        params:{
          classId:that.classId
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.performanceNotices = res.data.obj.performancenotices;
          that.stus = res.data.obj.stuperformances;
        }
      })
      .catch((e)=>{
        console.log(e);

      });
    },
    //重命名表现
    editPerformanceNotice(item){
      this.modal4 = true;
      this.currentPerformanceNotice = item;
      this.currentPerformanceNoticeModel.name = item.name;
      this.currentPerformanceNoticeModel.id = item.id;
    },
    editPerformanceNoticeOK(){
      var that = this;
      var params = new URLSearchParams(); 
      for(var key in this.currentPerformanceNoticeModel){
        params.append(key,this.currentPerformanceNoticeModel[key]);
      }
      this.$axios.post('tea/renamePerformanceNotice.aurora',params)
      .then((res)=>{
        if(res.data && res.data.success){
          that.$Message.success(res.data.msg);
          that.modal4 = false;
          that.currentPerformanceNotice.name = that.currentPerformanceNoticeModel.name;
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });

    },
    //删除一条表现
    deletePerformanceNotice(item){
      this.$Modal.confirm({
                    title: '确认删除该表现？',
                    content: '',
                    loading: true,
                    scrollable:true,
                    onOk: () => {
                      this.deletePerformanceNoticeOK(item.id);
                    }
                });
    },
    deletePerformanceNoticeOK(id){
      var that = this;
      this.$axios.get('tea/deletePerformanceNotice.aurora',{
        params:{
          id: id,
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.getPerformances();
          that.$Message.success(res.data.msg);
          that.$Modal.remove();
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
    },
    //改变表现分数
    changePerformanceScore(name,item){
      this.currentPerformance = item;
      this.currentPerformance.name = name;
      this.modal3 = true;
      this.currentPerformanceScore = this.currentPerformance.score;
      this.starCount = this.currentPerformance.score + 5;
    },
    changePerformanceScoreOK(){
      var that = this;
      this.$axios.get('tea/changePerformanceScore.aurora',{
        params:{
          id: that.currentPerformance.id,
          score: that.currentPerformanceScore
        }
      })
      .then((res)=>{
        if(res.data && res.data.success){
          that.currentPerformance.score = that.currentPerformanceScore;
          that.modal3 = false;
          that.$Message.success(res.data.msg);
        }else{
          alert('出错了，请重试或重新登录。');
        }
      })
      .catch((e)=>{
        console.log(e);
      });
      
    }
  },
  computed: {
    classId(){
      return this.$store.getters.getCurrentClassId;
    },
    checkinCode(){
      if(this.checkinNotice != null){
        var arr = [];
        for(var key in this.checkinNotice.checkincode){
          arr.push(this.checkinNotice.checkincode[key]);
        }
        this.onlineCheckinState.sum = this.checkinNotice.sum;
        return arr;
      }else{
        return [0,0,0,0]
      }
    },
    currentPerformanceUserName(){
      if(this.currentPerformance == null){
        return '';
      }else{
        return this.currentPerformance.name;
      }
    }
  },
  watch: {
    modal1(){
      if(this.modal1 == true){
        this.model.name = this.$moment(new Date()).format("YYYY.MM.DD");
      }
    }
  }
}
</script>

<style lang = "less" scoped>
  @bor-c:#c8c8c8;
  .performance {
    background: white;
    min-height: inherit;
    padding:2rem;
    .options {
      display: flex;
      justify-content: space-between;
      > div {
        display: flex;
        align-items: center;
      }
    }
    .main {
      margin-top:2rem;
      border:1px solid @bor-c;
      text-align: center;
      .main-left {
        border-right:1px solid @bor-c;
        > div {
          display: flex;
          justify-content: flex-start;
          padding-right:1rem;
          align-items: center;
          border-bottom:1px solid @bor-c;
          height: 4rem;
        }
      }
      .main-right {
        overflow:auto;
        .main-right-container {
          display:inline-block;
	        min-width:100%;
          > div {
            height: 4rem;
            border-bottom: 1px solid @bor-c;
            display: flex;
            > div {
              width:10rem;
              display: flex;
              justify-content: center;
              align-items:center;
              flex-direction: column;
            }
          }
        }
      }
      .main-container {
        min-height:40rem;
        > div {
          &:nth-child(odd){
            background-color:#eee;
          }
          &:hover {
           /*background-color:#eee;*/
          }
        }
      }
    /**具体样式**/
      /*左边每行*/
      .student{
        padding-right: 1rem;
        position: relative;
        > div {
          margin-left:1rem;
          
        }
        .hovershow {
            position: absolute;
            right:1rem;
        }
        &:hover {
          .hovershow {
            color:red;
          }
        }
        
      }
      /*右边第一行*/
      .performance-info {
        .hovershow {
          display:none;
          > * {
            background:#ddd;
            padding:.5rem;
            border-radius: 3px;
            &:hover {
              background:#ccc;
            }
          }
        }
        .hoverhidden {
          color: #aaa;
        }
        &:hover {
          .hovershow {
            display:block;
          }
          .hoverhidden {
            display: none;
          }
        }

      }
      
    }
    
  }
</style>



