<template>
  <div class = "classes">
    <v-header></v-header>
    <v-drawer></v-drawer>
    <div class = "main">
    <transition name="component-fade" mode="out-in">
    <router-view>
    </router-view>
    </transition>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
import drawer from '@/components/common/drawer'
import header from '@/components/classes/header'
import footer from '@/components/common/footer'
import main from '@/components/classes/main'
import setting from '@/components/classes/setting'

export default {
  name: 'index',
  data () {
    return {
      msg: '主页'
    }
  },
  created(){
    if(!this.$store.getters.getType && this.$store.getters.getType == -1){
      this.$router.push('/login');
    }
  },
  components:{
	'v-header':header,
  'v-footer':footer,
  'v-drawer':drawer
  },
  main:main,
  setting:setting
}
</script>


<style scoped>

  .classes {
        width: 100%;
        height: 945px;
        position: absolute;
        z-index: 0;
        background: url(../assets/images/bag6.jpg) top center no-repeat;
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
        width: calc(100vw + 20px);
        overflow:hidden;
        overflow-y:auto;
  }

.main {
    min-height:945px;
  }
  /**过渡效果**/
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .3s ease;
  }
.component-fade-enter, .component-fade-leave-to
  /* .component-fade-leave-active for below version 2.1.8 */ {
    opacity: 0;
  }
  
</style>



